import { Component, ElementRef, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { TableComponent } from '../../base/table/table/table.component';
import { IObjectString } from 'src/app/common/IObject';
import { FTOT0080StoreService } from 'src/app/services/FTOT0080/ftot0080-store.service';

@Component({
  selector: 'app-stot00802',
  templateUrl: './stot00802.component.html'
})
export class STOT00802Component extends BaseComponent implements OnInit {
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00802;

  constructor(protected loginInfo: LoginInfoService,
    private el: ElementRef,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0080Store: FTOT0080StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  @ViewChild("table") table: TableComponent | any;

  storeListChecked: IObjectString = {};
  tableInfo: IObjectString = {}

  lstSoordno : any = [];
  checkedlstall: any = [];

  async fnInit() {
    this.fnGetColumnBaseConfig();
    this.displayDataTableItem();
    await this.fnGetData();
  }

  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  /*buil table */
  displayDataTableItem() {
    this.tableInfo.data = this.lstSoordno;
    this.table.fnBuildBody();
  }

  fnGetColumnBaseConfig() {
    //header table infor detail
    this.tableInfo.columns = this.fnGetColumnInfo();
    this.tableInfo.rowSpan = 1;
    this.tableInfo.colNum = 2;
    this.table.fnBuildHeader();
  }

  fnGetColumnInfo() {
    var header = [];
    var column: IObjectString = {};

    column = {};
    column.title = "C";
    column.colName = "C";
    column.dataType = "checkbox";
    column.width = "80px";
    column.align = "center";
    header.push(column);

    column = {};
    column.title = this.formItemNm[1];
    column.colName = "CSTMRSVNO";
    column.dataType = "string";
    column.width = "200px";
    column.align = "center";
    header.push(column);

    return header;
  }

  //init load data
  async fnGetData() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0080ConditionDto" : {
        "DELIMTHDCD" : this.FTOT0080Store.DELIMTHDCD
      }
    }

    return await this.webService.callWs(Const.stot00802InitWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        return;
      } else {
        this.lstSoordno = response.lstSoordno;
        let checkbox: any = document.querySelector("#" + 'checkAll');
        checkbox.checked = false;
        this.displayDataTableItem();
      }
    })
  }

  fnCheckBoxChange($event: any) {
    let rowInfo = $event.rowInfo;
    let data = rowInfo.data;
    this.checkedlstall = [];

    if (data[rowInfo.column.colName] == true) {
      this.storeListChecked[rowInfo.rowIndex] = data;
    } else {
      delete this.storeListChecked[rowInfo.rowIndex];
    }
    for (let key in this.storeListChecked) {
      this.checkedlstall.push(this.storeListChecked[key]);
    }
    if (this.checkedlstall.length == this.tableInfo.data.length) {
      this.table.fnSetCheckAll(true, 'C');
    }else {
      let checkbox: any = document.querySelector("#" + 'checkAll');
      checkbox.checked = false;
    }
  }


  fnCheckAll(isCheck: any) {
    if(this.table.fnGetIsCheckAll("C") == false){
      this.storeListChecked = {};
    }
  }
  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00801])
  }

  btnConfirm(){
    this.FTOT0080Store.checkedlst = [];
    if (this.table.fnGetIsCheckAll("C")) {
      this.FTOT0080Store.checkedlst = this.tableInfo.data;
    } else {
      for (let key in this.storeListChecked) {
        this.FTOT0080Store.checkedlst.push(this.storeListChecked[key]);
      }
    }
    if(this.FTOT0080Store.checkedlst.length == 0) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.notifyError(this.appService, this.message.message["ME000113"], () => {
        return;
      })
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "rows": this.FTOT0080Store.checkedlst,
      "SYSKBN1": this.getSystemEntity("SYSKBN1"),
      "SYSKBN49": this.getSystemEntity("SYSKBN49"),
      "SYSFLG4": this.getSystemEntity("SYSFLG4"),
      "SYSFLG26": this.getSystemEntity("SYSFLG26")
    }

    return this.webService.callWs(Const.stot00802RegistWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        return;
      } else {
        this.notifySuccess(this.appService, this.messageEntity.message["MI000008"], () => {
          this.fnGetData();
        })
      }
    })
  }

}
