import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { FTIN0010StoreService } from 'src/app/services/FTIN0010/ftin0010-store.service';

@Component({
  selector: 'app-stin00104',
  templateUrl: './stin00104.component.html'
})
export class STIN00104Component extends BaseComponent implements OnInit {
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STIN00104;


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTIN0010Store : FTIN0010StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  ITEMCD = "";
  ITEMNM = "";
  LCTNCD = "";
  CSTMRSVNO = "";
  ARVLPLNREMAINQTY = "";
  incodeMessage: string = "";
  ARVLPLNQTY : number = 0;
  storestArvlrsltQty : number = 0;
  storestArvlrsltQtybefore : number = 0;
  hideArvlplnremain: boolean = true;
  @ViewChild("incode") incode: InputComponent | any;

  fnInit() {
    this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
    this.storestArvlrsltQty = this.FTIN0010Store.ARVLRSLTQTY;
    this.ITEMCD = this.FTIN0010Store.ITEMCD;
    this.ITEMNM = this.FTIN0010Store.ITEMNM;
    this.incode.focusIn();
    this.LCTNCD = this.FTIN0010Store.LCTNCD ;
    this.CSTMRSVNO = Ultility.isEmpty(this.FTIN0010Store.CSTMRSVNO) ?  "" : this.FTIN0010Store.CSTMRSVNO;
    this.ARVLPLNQTY = this.FTIN0010Store.ARVLPLNQTY;
    if (this.FTIN0010Store.SHIPFLG == "1") {
      this.ARVLPLNREMAINQTY = this.FTIN0010Store.ARVLPLNREMAINQTY;
      this.hideArvlplnremain = true;
    } else {
      this.hideArvlplnremain = false;
    }
  }


  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'incode': {
        this.fnArvlrsQtyEnt(this.incode.getVal());
        break;
      }
    }
  }

  fnArvlrsQtyEnt(incode: any) {
    var itemcd = this.FTIN0010Store.ITEMCD;
    this.FTIN0010Store.ARVLRSLTQTYBEFORE = this.FTIN0010Store.ARVLRSLTQTY;
    if (Ultility.isEmpty(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.notifyError(this.appService, this.message.message["ME000007"].replace("%1", this.formItemNm[6]),() => {
        this.incode.focusIn();
        this.incode.selectAll();
		this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
      })
      return;
    }
   if(incode.toUpperCase() == itemcd && (Ultility.isEmpty(itemcd) == false)){
      this.storestArvlrsltQtybefore = this.storestArvlrsltQty;
      this.incode.setVal(this.storestArvlrsltQty + 1);
      this.storestArvlrsltQty = this.incode.getVal();
      this.incode.focusIn();
      this.incode.selectAll();
      if(Ultility.isEmpty(this.ARVLPLNREMAINQTY) == false || this.FTIN0010Store.ARVLPLNREMAINQTY != 0){
        if (this.FTIN0010Store.ARVLPLNQTY - this.FTIN0010Store.ARVLEDQTY < this.storestArvlrsltQty) {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.notifyError(this.appService, this.message.message["ME000031"].replace("%1", ""), () => {
            this.storestArvlrsltQty = this.storestArvlrsltQtybefore ;
            this.incode.focusIn();
            this.incode.selectAll();
            this.incode.setVal("");
          })
        }
      }
      this.FTIN0010Store.ARVLRSLTQTY = this.storestArvlrsltQty;
      return;
    }
    if (Ultility.isInteger(incode)) {
      if (incode <= 0) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, this.message.message["ME000010"].replace("%1", this.formItemNm[7]).replace("%2", "1"),() => {
          this.incode.focusIn();
          this.incode.selectAll();
          this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
        })
        return;
      } else if (this.fnIsDecimal(incode) == true) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, this.message.message["ME000130"],() => {
          this.incode.focusIn();
          this.incode.selectAll();
          this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
        })
        return;
      } else {
        this.FTIN0010Store.ARVLRSLTQTY = incode;
      }
    }else{
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.notifyError(this.appService, this.message.message["ME000007"].replace("%1", this.formItemNm[6]),() => {
        this.incode.focusIn();
        this.incode.selectAll();
        this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
      })
      return;
    }
    if(this.fnCheckSuryo() == -1){
      return;
    }else{
      this.fnCheckBarcode();
    }
  }

  fnCheckBarcode(){
    var itemcd = this.FTIN0010Store.ITEMCD;
    var shipflg = this.FTIN0010Store.SHIPFLG;
    var arvledqty = this.FTIN0010Store.ARVLEDQTY;
    var arvlplnQty = this.FTIN0010Store.ARVLPLNQTY;
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stin0010Condition": {
        "SCANCD": this.incode.getVal(), 
        "ITEMCD": itemcd,
        "ARVLRSLTQTY": this.FTIN0010Store.ARVLRSLTQTY,
        "ARVLRSLTQTYBEFORE" : this.FTIN0010Store.ARVLRSLTQTYBEFORE,
        "SHIPFLG" : shipflg,
        "ARVLEDQTY" : arvledqty,
        "ARVLPLNQTY" : arvlplnQty
      }
    }
    return this.webService.callWs(Const.stin00104CheckBarCodeWs , request , (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
            this.incode.focusIn();
            this.incode.selectAll();
            this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
          })
        return;
      }else{
        this.incode.focusIn();
        this.incode.selectAll();
        this.incode.setVal(response.ARVLRSLTQTY);
        this.FTIN0010Store.ARVLRSLTQTY = response.ARVLRSLTQTY;
        this.storestArvlrsltQty = this.incode.getVal();
      }
    })
  }

  fnIsDecimal(dValue: any) {
    if (dValue - Math.floor(dValue) != 0) {
      return true;
    }
    return false;
  }

  fnCheckSuryo() {
    var Err = 0
    if (this.FTIN0010Store.SHIPFLG != "1") {
      return;
    }

    if (this.getSystemEntity("SYSFLG1") == "1") {
     if (this.FTIN0010Store.ARVLPLNQTY - this.FTIN0010Store.ARVLEDQTY > this.FTIN0010Store.ARVLRSLTQTY) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyConfirm(this.appService,  this.message.message["MW000007"].replace("%1", this.formItemNm[7]).replace("%2", ""), () => {
          this.incode.focusIn();
          this.incode.selectAll();
          this.fnUpdate();
        },() => {
          this.storestArvlrsltQty = parseInt(this.incode.getVal()) ;
          this.incode.focusIn();
          this.incode.selectAll();
          this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
        })
        Err = -1 ;
      }
    } else {
      if (this.FTIN0010Store.ARVLPLNQTY - this.FTIN0010Store.ARVLEDQTY > this.FTIN0010Store.ARVLRSLTQTY) {
        this.FTIN0010Store.ARVLRSLTQTY = this.FTIN0010Store.ARVLRSLTQTYBEFORE;
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, this.message.message["ME000032"].replace("%1", this.formItemNm[7]).replace("%2", ""), () => {
          this.incode.focusIn();
          this.incode.selectAll();
          this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
        })
        Err = -1 ;
      }
    }
    if (this.FTIN0010Store.ARVLPLNQTY - this.FTIN0010Store.ARVLEDQTY < this.FTIN0010Store.ARVLRSLTQTY) {
      this.FTIN0010Store.ARVLRSLTQTY = this.FTIN0010Store.ARVLRSLTQTYBEFORE;
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.notifyError(this.appService, this.message.message["ME000031"].replace("%1", ""), () => {
        this.incode.focusIn();
        this.incode.selectAll();
        this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
      })
      Err = -1 ;
    }
    return Err;
  }

  fnCheckUpdate() {
    var incode = this.incode.getVal();
    if(Ultility.isEmpty(incode) == false){
      if (Ultility.isInteger(incode) == false) {
        if(incode < 0){
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.notifyError(this.appService, this.message.message["ME000010"].replace("%1", this.formItemNm[7]).replace("%2","1"), () => {
            this.incode.focusIn();
            this.incode.selectAll();
            this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
          })
          return;
        }else{
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.notifyError(this.appService, this.message.message["ME000007"].replace("%1", this.formItemNm[7]), () => {
            this.incode.focusIn();
            this.incode.selectAll();
            this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
          })
          return;
        }
      }else if (this.fnIsDecimal(incode) == true) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, this.message.message["ME000130"], () => {
          this.incode.focusIn();
          this.incode.selectAll();
          this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
        })
        return;
      } else if(incode.length > 9){
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, this.message.message["ME000049"].replace("%1", 9).replace("%2", 0), () => {
          this.incode.focusIn();
          this.incode.selectAll();
          this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
        })
        return;
      }else {
        if(incode == 0){
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.notifyError(this.appService, this.message.message["ME000010"].replace("%1", this.formItemNm[7]).replace("%2","1"), () => {
            this.incode.focusIn();
            this.incode.selectAll();
            this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
          })
          return;
        }
        this.FTIN0010Store.ARVLRSLTQTY = incode;
      }
    }else{
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.notifyError(this.appService, this.message.message["ME000007"].replace("%1", this.formItemNm[7]), () => {
        this.incode.focusIn();
        this.incode.selectAll();
        this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
      })
      return;
    }
    if(this.fnCheckSuryo() == -1){
      return;
    }else{
      this.fnUpdate();
    }
  }

  fnUpdate(){
    var siplnno = this.FTIN0010Store.SIPLNNO;
    var itemcd = this.FTIN0010Store.ITEMCD;
    var qltycd = this.FTIN0010Store.QLTYCD;
    var shipflg = this.FTIN0010Store.SHIPFLG;
    var limitdate = this.FTIN0010Store.LIMITDATE;
    var stck1 = this.FTIN0010Store.STCKMNGKEY1;
    var stck2 = this.FTIN0010Store.STCKMNGKEY2;
    var stck3 = this.FTIN0010Store.STCKMNGKEY3;
    var sidltlno = this.FTIN0010Store.SIDTLNO;
    var lctncd = this.FTIN0010Store.LCTNCD;
    var arvlrsltQty = this.incode.getVal();
    var sysflg4 = this.getSystemEntity("SYSFLG4");
    var sidivkbn = this.getSystemEntity("SIDIVKBN");
    var soordno = this.FTIN0010Store.SOORDNO;
    var commonlotdisflg  = this.getSystemEntity("CommonLotDispFlg");

    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stin0010Condition": {
        "SIPLNNO" : Ultility.isEmpty(siplnno) ? "" : siplnno,
        "ITEMCD" : Ultility.isEmpty(itemcd) ? "" : itemcd,
        "QLTYCD" : Ultility.isEmpty(qltycd) ? "" : qltycd,
        "LIMITDATE" : Ultility.isEmpty(limitdate) ? "" : limitdate,
        "STCKMNGKEY1" : Ultility.isEmpty(stck1) ? "" : stck1,
        "STCKMNGKEY2" : Ultility.isEmpty(stck2) ? "" : stck2,
        "STCKMNGKEY3" : Ultility.isEmpty(stck3) ? "" : stck3,
        "SIDTLNO" : Ultility.isEmpty(sidltlno) ? "" : sidltlno,
        "LCTNCD" : Ultility.isEmpty(lctncd) ? "" : lctncd,
        "SHIPFLG" : Ultility.isEmpty(shipflg) ? "" : shipflg,
        "ARVLRSLTQTY" : Ultility.isEmpty(arvlrsltQty) ? "" : arvlrsltQty,
        "SOORDNO" : Ultility.isEmpty(soordno) ? "" : soordno,
        "CommonLotDispFlg": commonlotdisflg ,
      },
      "SYSFLG4" :  Ultility.isEmpty(sysflg4) ? "" : sysflg4,
      "SIDIVKBN" :  Ultility.isEmpty(sidivkbn) ? "" : sidivkbn,
    }

    return this.webService.callWs(Const.stin00104UpdateWs , request , (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
            this.incode.focusIn();
            this.incode.selectAll();
            this.incode.setVal(this.FTIN0010Store.ARVLRSLTQTY);
          })
        return;
      }else{
          this.FTIN0010Store.clear();
          this.router.navigate([DisplayScreenID.STIN00101]);
      }
    })
  }

  btnBack() {
    this.FTIN0010Store.LCTNCD = "";
    this.router.navigate([DisplayScreenID.STIN00102]);
  }
  
  btnConfirm(){
    this.fnCheckUpdate();
  }
}
