import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { FTIN0010StoreService } from 'src/app/services/FTIN0010/ftin0010-store.service';

@Component({
  selector: 'app-stin00103',
  templateUrl: './stin00103.component.html'
})
export class STIN00103Component extends BaseComponent implements OnInit {
  [x: string]: any;
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STIN00103;


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTIN0010Store : FTIN0010StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  @ViewChild("limitdate") limitdate: InputComponent | any
  @ViewChild("stckmngkey1") stckmngkey1: InputComponent | any
  @ViewChild("stckmngkey2") stckmngkey2: InputComponent | any
  @ViewChild("stckmngkey3") stckmngkey3: InputComponent | any
  ITEMCD = "";
  ITEMNM = "";
  CSTMRSVNO = "";
  limitdateMessage = "";
  stckmngkey1Message = "";
  stckmngkey2Message = "";
  stckmngkey3Message = "";

  hideLimitdate: Boolean = true;
  hideStckmngkey1: Boolean = true;
  hideStckmngkey2: Boolean = true;
  hideStckmngkey3: Boolean = true;

  fnInit() {
    this.ITEMCD  = this.FTIN0010Store.ITEMCD;
    this.ITEMNM  = this.FTIN0010Store.ITEMNM;
    this.CSTMRSVNO = Ultility.isEmpty(this.FTIN0010Store.CSTMRSVNO) ?  "" : this.FTIN0010Store.CSTMRSVNO;
    if(this.FTIN0010Store.prevPage !=""){
      if(this.FTIN0010Store.prevPage == "STIN00102"){
        this.limitdate.focusIn();
        this.limitdate.setVal(this.FTIN0010Store.LIMITDATE);
        this.stckmngkey1.setVal(this.FTIN0010Store.STCKMNGKEY1);
        this.stckmngkey2.setVal(this.FTIN0010Store.STCKMNGKEY2);
        this.stckmngkey3.setVal(this.FTIN0010Store.STCKMNGKEY3);
      }
    }
    if (this.getSystemEntity("CommonLotDispFlg") == 1) {
      if (this.getSystemEntity("SYSKBN3") == "1") {
        this.hideLimitdate = false;
        this.stckmngkey1.focusIn();
      } else {
        this.hideLimitdate = true;
        this.limitdate.setVal(this.FTIN0010Store.LIMITDATE);
        this.limitdate.focusIn();
      }
      if (this.getSystemEntity("SYSKBN4") == "1") {
        this.hideStckmngkey1 = false;
        this.stckmngkey2.focusIn();
      } else {
        this.hideStckmngkey1 = true;
        this.stckmngkey1.setVal(this.FTIN0010Store.STCKMNGKEY1);
      }
      if (this.getSystemEntity("SYSKBN5") == "1") {
        this.hideStckmngkey2 = false;
        this.stckmngkey3.focusIn();
      } else {
        this.hideStckmngkey2 = true;
        this.stckmngkey2.setVal(this.FTIN0010Store.STCKMNGKEY2);
      }
      if (this.getSystemEntity("SYSKBN6") == "1") {
        this.hideStckmngkey3 = false;
        this.stckmngkey1.focusIn();
      } else {
        this.hideStckmngkey3 = true;
        this.stckmngkey3.setVal(this.FTIN0010Store.STCKMNGKEY3);
      }
      if (this.getSystemEntity("SYSKBN3") == "1"
        && this.getSystemEntity("SYSKBN4") == "1"
        && this.getSystemEntity("SYSKBN5") == "1"
        && this.getSystemEntity("SYSKBN6") == "1") {
        this.hideLimitdate = false;
        this.hideStckmngkey1 = false;
        this.hideStckmngkey2 = false;
        this.hideStckmngkey3 = false;
      }
    }
  }

  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch(currentFocus) {
      case 'limitdate' : 
        this.fnClearMessage();
        let date = this.limitdate.getVal();
        if(Ultility.isDate(date)){
          this.limitdate.setVal(date);
        }
        if(this.FTIN0010Store.LIMITDATEKBN != 0 && Ultility.isEmpty(date)){
          Ultility.handyCtrl(this.ledService, Const.LedRed , true , Const.SoundWarning);
          this.limitdateMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[4]);
          if(date = this.FTIN0010Store.LIMITDATE){
            this.limitdate.focusIn();
            this.limitdate.setVal(this.FTIN0010Store.LIMITDATE);
          }else{
            this.limitdate.focusIn();
            this.limitdate.setVal("");
          }
          return;
        }
        if(Ultility.isEmpty(this.limitdate.getVal()) == false){
          if(Ultility.isDate(this.limitdate.getVal()) == false){
            this.limitdateMessage = this.message.message["ME000006"].replace("%1", this.formItemNm[4]);
              if(date = this.FTIN0010Store.LIMITDATE){
                this.limitdate.focusIn();
                this.limitdate.setVal(this.FTIN0010Store.LIMITDATE);
              }else{
                this.limitdate.focusIn();
                this.limitdate.setVal("");
              }
            return;
          }
          this.FTIN0010Store.LIMITDATE = this.limitdate.getVal();
        }
        this.stckmngkey1.selectAll();
        this.stckmngkey1.focusIn();
        break;
      case "stckmngkey1" : 
        this.fnClearMessage();
        let stck1 = this.stckmngkey1.getVal();
        if(this.getSystemEntity("SYSKBN4") == "2" && Ultility.isEmpty(stck1)){
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.stckmngkey1Message = this.message.message["ME000004"].replace("%1" , this.formItemNm[5]);
            if(stck1 = this.FTIN0010Store.STCKMNGKEY1){
              this.stckmngkey1.focusIn();
              this.stckmngkey1.setVal(this.FTIN0010Store.STCKMNGKEY1);
            }else{
              this.stckmngkey1.focusIn();
              this.stckmngkey1.setVal("");
            }
      
          return;
        }
        if(stck1.length > 40){
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.stckmngkey1Message = this.message.message["ME000008"].replace("%1" , this.formItemNm[5]).replace("%2", "40");
            if(stck1 = this.FTIN0010Store.STCKMNGKEY1){
              this.stckmngkey1.focusIn();
              this.stckmngkey1.setVal(this.FTIN0010Store.STCKMNGKEY1);
            }else{
              this.stckmngkey1.focusIn();
              this.stckmngkey1.setVal("");
            }
          return;
        }
        this.FTIN0010Store.STCKMNGKEY1 = this.stckmngkey1.getVal();
        this.stckmngkey2.selectAll();
        this.stckmngkey2.focusIn();
        break;
      case 'stckmngkey2':
        this.fnClearMessage();
        let stck2 = this.stckmngkey2.getVal();
        if(this.getSystemEntity("SYSKBN5") == "2" && Ultility.isEmpty(stck2)){
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.stckmngkey2Message = this.message.message["ME000004"].replace("%1" , this.formItemNm[6]);
            if(stck2 = this.FTIN0010Store.STCKMNGKEY2){
              this.stckmngkey2.focusIn();
              this.stckmngkey2.setVal(this.FTIN0010Store.STCKMNGKEY2);
            }else{
              this.stckmngkey2.focusIn();
              this.stckmngkey2.setVal("");
            }
          return;
        }
        if(stck2.length > 40){
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.stckmngkey2Message = this.message.message["ME000008"].replace("%1" , this.formItemNm[6]).replace("%2", "40");
            if(stck2 = this.FTIN0010Store.STCKMNGKEY2){
              this.stckmngkey2.focusIn();
              this.stckmngkey2.setVal(this.FTIN0010Store.STCKMNGKEY2);
            }else{
              this.stckmngkey2.focusIn();
              this.stckmngkey2.setVal("");
            }
          return;
        }
        this.FTIN0010Store.STCKMNGKEY2 = this.stckmngkey2.getVal();
        this.stckmngkey3.selectAll();
        this.stckmngkey3.focusIn();
        break;
      case 'stckmngkey3':
        this.fnClearMessage();
        let stck3 = this.stckmngkey3.getVal();
        if(this.getSystemEntity("SYSKBN6") == "2" && Ultility.isEmpty(stck3)){
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.stckmngkey3Message = this.message.message["ME000004"].replace("%1" , this.formItemNm[7]);
            if(stck3 = this.FTIN0010Store.STCKMNGKEY3){
              this.stckmngkey3.focusIn();
              this.stckmngkey3.setVal(this.FTIN0010Store.STCKMNGKEY3);
            }else{
              this.stckmngkey3.focusIn();
              this.stckmngkey3.setVal("");
            }
          return;
        }
        if(stck3.length > 40){
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.stckmngkey3Message = this.message.message["ME000008"].replace("%1" , this.formItemNm[7]).replace("%2", "40");
            if(stck3 = this.FTIN0010Store.STCKMNGKEY3){
              this.stckmngkey3.focusIn();
              this.stckmngkey3.setVal(this.FTIN0010Store.STCKMNGKEY3);
            }else{
              this.stckmngkey3.focusIn();
              this.stckmngkey3.setVal("");
            }
          return;
        }
        this.FTIN0010Store.STCKMNGKEY3 = this.stckmngkey3.getVal();
        this.limitdate.selectAll();
        this.limitdate.focusIn();
        break;
      default:
        this.fnClearMessage();
        break;
    }    
  }

  fnClearMessage(){
    this.limitdateMessage = "";
    this.stckmngkey1Message = "";
    this.stckmngkey2Message = "";
    this.stckmngkey3Message = "";
  }

  btnBack() {
    this.FTIN0010Store.clear();
    this.FTIN0010Store.prevPage = "STIN00103";
    this.router.navigate([DisplayScreenID.STIN00101]);
  }

  fnCheckLot() {
    var itemcd = this.FTIN0010Store.ITEMCD;
    var qltycd = this.FTIN0010Store.QLTYCD;
    var date = this.limitdate.getVal();
    var limitdatekbn = this.FTIN0010Store.LIMITDATEKBN;
    var stck1 = this.stckmngkey1.getVal();
    var stck2 = this.stckmngkey2.getVal();
    var stck3 = this.stckmngkey3.getVal();
    var syskbn3 = this.getSystemEntity("SYSKBN3");
    var syskbn4 = this.getSystemEntity("SYSKBN4");
    var syskbn5 = this.getSystemEntity("SYSKBN5");
    var syskbn6 = this.getSystemEntity("SYSKBN6");
    var myclorflg = this.FTIN0010Store.MYCLORFLG;
    var shipflg = this.FTIN0010Store.SHIPFLG;
    var ecflg = this.FTIN0010Store.ECFLG;
    var holdlctncd = this.FTIN0010Store.HOLDLCTNCD;
    var arvltypemsg = this.FTIN0010Store.ARVLTYPEMSG;
    var cstmRsvNo = this.FTIN0010Store.CSTMRSVNO;
    var sipLst = this.FTIN0010Store.sipLst;
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stin0010Condition": {
        "ITEMCD": Ultility.isEmpty(itemcd) ? "" : itemcd,
        "QLTYCD": Ultility.isEmpty(qltycd) ? "" : qltycd,
        "LIMITDATE": Ultility.isEmpty(date) ? "" : date,
        "STCKMNGKEY1": Ultility.isEmpty(stck1) ? "" : stck1,
        "STCKMNGKEY2": Ultility.isEmpty(stck2) ? "" : stck2,
        "STCKMNGKEY3": Ultility.isEmpty(stck3) ? "" : stck3,
        "LIMITDATEKBN": Ultility.isEmpty(limitdatekbn) ? "" : limitdatekbn,
        "MYCLORFLG": Ultility.isEmpty(myclorflg) ? "" : myclorflg,
        "SHIPFLG" : Ultility.isEmpty(shipflg) ? "" : shipflg,
        "ECFLG" : Ultility.isEmpty(ecflg) ? "" : ecflg,
        "HOLDLCTNCD" : Ultility.isEmpty(holdlctncd) ? "" : holdlctncd,
        "ARVLTYPEMSG" : Ultility.isEmpty(arvltypemsg) ? "" : arvltypemsg,
        "CSTMRSVNO" : Ultility.isEmpty(cstmRsvNo) ? "" : cstmRsvNo,
        "CommonLotDispFlg": this.getSystemEntity("CommonLotDispFlg"),
      },
      "sipLst" : sipLst,
      "SYSKBN3": syskbn3,
      "SYSKBN4": syskbn4,
      "SYSKBN5": syskbn5,
      "SYSKBN6": syskbn6,
    }

    return this.webService.callWs(Const.stin00103CheckLotWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.limitdate.focusIn();
        })
        return;
      }else{
        if (response.ERRORMSG == "MW000001") {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.notifyConfirm(this.appService, this.message.message["MW000001"].replace("%1", ""), () => {
            this.FTIN0010Store.QLTYCD = qltycd;
            this.FTIN0010Store.LIMITDATE = response.LIMITDATE;
            this.FTIN0010Store.STCKMNGKEY1 = response.STCKMNGKEY1;
            this.FTIN0010Store.STCKMNGKEY2 = response.STCKMNGKEY2;
            this.FTIN0010Store.STCKMNGKEY3 = response.STCKMNGKEY3;
            this.FTIN0010Store.SIDTLNO = response.SIDTLNO;
            this.FTIN0010Store.ARVLPLNQTY = response.ARVLPLNQTY;
            this.FTIN0010Store.ARVLPLNREMAINQTY = response.ARVLPLNREMAINQTY;
            this.FTIN0010Store.LCTNCD = response.stin0010Condition.LCTNCD;
            this.FTIN0010Store.FRLCTNCD = response.stin0010Condition.FRLCTNCD;
            this.FTIN0010Store.ARVLTYPEMSG = response.stin0010Condition.ARVLTYPEMSG;
            this.FTIN0010Store.CSTMRSVNO = response.stin0010Condition.CSTMRSVNO;
            this.FTIN0010Store.prevPage = "STIN00103";
            this.router.navigate([DisplayScreenID.STIN00102]);
          }, () => { 
            this.FTIN0010Store.clear();
            this.FTIN0010Store.prevPage = "STIN00103";
            this.router.navigate([DisplayScreenID.STIN00101]);
          })
          return;
        }else{
          this.FTIN0010Store.QLTYCD = qltycd;
          this.FTIN0010Store.LIMITDATE = response.LIMITDATE;
          this.FTIN0010Store.STCKMNGKEY1 = response.STCKMNGKEY1;
          this.FTIN0010Store.STCKMNGKEY2 = response.STCKMNGKEY2;
          this.FTIN0010Store.STCKMNGKEY3 = response.STCKMNGKEY3;
          this.FTIN0010Store.SIDTLNO = response.SIDTLNO;
          this.FTIN0010Store.ARVLPLNQTY = response.ARVLPLNQTY;
          this.FTIN0010Store.ARVLPLNREMAINQTY = response.ARVLPLNREMAINQTY;
          this.FTIN0010Store.LCTNCD = response.stin0010Condition.LCTNCD;
          this.FTIN0010Store.FRLCTNCD = response.stin0010Condition.FRLCTNCD;
          this.FTIN0010Store.ARVLTYPEMSG = response.stin0010Condition.ARVLTYPEMSG;
          this.FTIN0010Store.CSTMRSVNO = response.stin0010Condition.CSTMRSVNO;
          this.FTIN0010Store.prevPage = "STIN00103";
          this.router.navigate([DisplayScreenID.STIN00102]);
        }
      }
    })
  }

  btnCheckLot(){
    this.fnCheckLot();
  }
}
