import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import * as Const from '../../../common/Const';
import { IObjectString } from 'src/app/common/IObject';
import { Ultility } from 'src/app/services/common/ultility.service';
import { LedService } from 'src/app/services/common/led.service';
import { AppService } from 'src/app/services/common/app.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { NgbAccordion } from '@ng-bootstrap/ng-bootstrap';
import { FTCM0020StoreService } from 'src/app/services/FTCM0020/ftcm0020-store.service';
import { FTOT0010StoreService } from 'src/app/services/FTOT0010/ftot0010-store.service';

@Component({
  selector: 'app-stcm00203',
  templateUrl: './stcm00203.component.html'
})

export class STCM00203Component extends BaseComponent implements OnInit {
  @ViewChild("ngbAccordion") ngbAccordion: NgbAccordion | any;

  constructor(protected loginInfo: LoginInfoService,
    private FTCM0020Store: FTCM0020StoreService,
    protected FTOT00101Store: FTOT0010StoreService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    protected appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STCM00203;
  listParentMenu = [];
  listChildMenu = [];
  listTemp = [];
  listIcon: IObjectString = Const.menuIcons;
  listBackground: IObjectString = Const.menuColor;

  cstmcd = "";
  lstCstmcd = [];

  mainCstmcd = "";
  brnchcd = "";
  lstBrnchcd = [];
  mainBrnchcd = "";
  lang = "";
  lstLanguage = [];
  mainLang = "";
  JOBKBN = "";
  showNotify: boolean = false;
  index = 0;

  fnInit() {
    this.JOBKBN = this.appService.JOBKBN;
    this.listParentMenu = this.appService.listParentMenu;
    this.listTemp = this.appService.listChildMenu[this.JOBKBN];
    if (this.FTOT00101Store.SOORDNO != "") {
      this.appService.JOBKBN = "83";
      this.listTemp = this.appService.listChildMenu[this.appService.JOBKBN];
      for (var i in this.listTemp) {
        if (this.listTemp[i]["FUNCID"] == "FTOT0020" || this.listTemp[i]["FUNCID"] == "FTOT0040") {
          this.listChildMenu.push(this.listTemp[i])
        }
      }
      this.index = this.listChildMenu.length + 1;
      this.showNotify = true;
    } else {
      this.listChildMenu = this.appService.listChildMenu[this.JOBKBN];
    }
  }

  childMenuClick = (SCRNID: string, idx: number) => {
    Ultility.handyCtrl(this.ledService, Const.LedGreen, true, Const.SoundSuccess);
    this.appService.IDXMENU = idx;
    this.appService.SCRNID = SCRNID;
    if (this.FTOT00101Store.SOORDNO != "" && SCRNID == "STOT00401") {
      this.router.navigate([DisplayScreenID.STOT00402]);
    } else {
      this.router.navigate([SCRNID]);
    }
  }

  clickMenu() {
    this.appService.JOBKBN = "82";
    this.FTOT00101Store.clear();
    this.router.navigate([DisplayScreenID.STOT00101]);
  }

  fnDestroy() {

  }

  btnBack() {
    this.FTOT00101Store.clear();
    this.router.navigate([DisplayScreenID.STCM00202]);
  }
}
