import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { TableComponent } from '../../base/table/table/table.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0040StoreService } from 'src/app/services/FTOT0040/ftot0040-store.service';

@Component({
  selector: 'app-stot00407',
  templateUrl: './stot00407.component.html'
})
export class STOT00407Component extends BaseComponent implements OnInit {

  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0040STORE: FTOT0040StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  incodeMessage: string = "";
  @ViewChild("incode") incode: InputComponent | any;
  ITEMNM: string = "";
  SHIPMNTORDQTY: string = "";
  SHIPMNTRSLTQTY: string = "";
  GIFTDETAIL: string = "";
  LIQUIDDETAIL: string = "";
  BROKENDETAIL: string = "";
  WRAPPINGFLG: string = "";
  HEMMING: string = "";
  ADDITEMFLG: string = "";
  SIZE: string = "";
  COLOR: string = "";
  listShipment: any = [];
  optionlisttemp: any = [];
  optionlist: string = "";
  linkImg: string = "";

  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00407;

  fnInit() {
    
    this.listShipment = this.FTOT0040STORE.listShipment;
    this.ITEMNM = this.listShipment[this.FTOT0040STORE.NowIndex].ITEMNM;
    this.SHIPMNTORDQTY = this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTORDQTY;
    this.SHIPMNTRSLTQTY = this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY;
    this.GIFTDETAIL = this.listShipment[this.FTOT0040STORE.NowIndex].GIFTDETAIL == 1 ? this.formItemNm[8] : '';
    this.LIQUIDDETAIL = this.listShipment[this.FTOT0040STORE.NowIndex].LIQUIDDETAIL == 1 ? this.formItemNm[9] : '';
    this.BROKENDETAIL = this.listShipment[this.FTOT0040STORE.NowIndex].BROKENDETAIL == 1 ? this.formItemNm[10] : '';
    this.WRAPPINGFLG = this.listShipment[this.FTOT0040STORE.NowIndex].WRAPPINGFLG == 1 ? this.formItemNm[11] : '';
    this.HEMMING = this.listShipment[this.FTOT0040STORE.NowIndex].HEMMING == 1 ? this.formItemNm[12] : '';
    this.ADDITEMFLG = this.listShipment[this.FTOT0040STORE.NowIndex].ADDITEMFLG == 1 ? this.formItemNm[13] : '';
    this.SIZE = this.listShipment[this.FTOT0040STORE.NowIndex].SIZE;
    this.COLOR = this.listShipment[this.FTOT0040STORE.NowIndex].COLOR;
    this.optionlist = this.listShipment[this.FTOT0040STORE.NowIndex].OPTIONVAL;
    this.linkImg = "http://webcoban.vn/image/flower.gif";
    
    if (!Ultility.isEmpty(this.optionlist)) {
      this.optionlisttemp = this.FTOT0040STORE.OPTIONLST;
    } else {
      this.optionlisttemp = [];
    }
  }

  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'incode':
        break;
    }
  }
  
  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00406]);
  }
}
