import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { IObjectString } from 'src/app/common/IObject';
import { MenuService } from './menu.service';

@Injectable({
  providedIn: 'root'
})
export class AppService {
  // For menu
  menuParentNo: string = "";
  menuChildNo: string = "";
  listParentMenu = [];
  listChildMenu:IObjectString = {};
  childMenuHeaderText: string = "";
  childMenuHeaderIcon: string = "";
  childMenuHeaderNo: string = "";
  JOBKBN: string = "";
  SCRNID: string = "";
  IDXMENU: number = 0;


  // For header
  appService: IObjectString = {};
  appServiceSub: Subject<IObjectString> = new Subject<IObjectString>();

  // For modal
  appServiceModal: IObjectString = {};
  appServiceModalSub: Subject<IObjectString> = new Subject<IObjectString>();
  storedInput: any;

  constructor(private menu: MenuService) {
    this.appServiceSub.subscribe((value: IObjectString) => {
      this.appService = value;
    });

    this.appServiceModalSub.subscribe((value: IObjectString) => {
      this.appServiceModal = value;
    });
  }


  // Show hide header menu
  toggle(app: IObjectString) {
    this.appServiceSub.next(app);
  }

  // Show hide modal
  modal(modalContext: IObjectString) {
    this.appServiceModalSub.next(modalContext);
  }

  clear() {
    this.menu.clear();
    this.menuParentNo = "";
    this.menuChildNo = "";
    this.childMenuHeaderText = ""
    this.childMenuHeaderIcon = ""
    this.childMenuHeaderNo = "";
    this.listParentMenu = [];
    this.listChildMenu = [];
    this.JOBKBN = "";
  }

}
