import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { FTOT0010StoreService } from 'src/app/services/FTOT0010/ftot0010-store.service';
import { IObjectString } from 'src/app/common/IObject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-stot00105',
  templateUrl: './stot00105.component.html'
})
export class STOT00105Component extends BaseComponent implements OnInit {


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected FTOT00101Store: FTOT0010StoreService,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  @ViewChild("incode") incode: InputComponent | any;
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00105;
  numEC: string = "";
  btncase: string = "";
  typeKey: string = "";
  btnstyle: string = "";
  incodeMessage: string = "";
  jancd: string = "";
  itemnm: string = "";
  itemrnm: string = "";
  pckordqty: string = "";
  size: string = "";
  color: string = "";
  optionlst: string = "";
  labelPckrsltqty: number = 0;
  showBtn: boolean = true;
  optionColor: boolean = false;
  showbtnShortage: boolean = false;
  storeCd: IObjectString = {};
  storeQty: number = 0;
  storePckrslqty: number = 0;
  URL: String = "";
  optionlsttemp1: any = [];

  async fnInit() {
    this.storeQty = 0;
    if (this.FTOT00101Store.SCREENPCK == "1") {
      this.showBtn = true;
      this.showbtnShortage = true;
    } else {
      this.showBtn = false;
      this.showbtnShortage = false;
    }

    if (this.FTOT00101Store.BEFORESCREEN == "STOT00106") {
      this.jancd = this.FTOT00101Store.JANCD;
      this.color = this.FTOT00101Store.COLOR;
      this.size = this.FTOT00101Store.SIZE;
      this.itemnm = this.FTOT00101Store.ITEMNM;
      this.pckordqty = this.FTOT00101Store.PCKORDQTY;
      this.optionlst = this.FTOT00101Store.OPTIONLST;
      this.storeCd.itemcd = this.FTOT00101Store.ITEMCD;
      this.storeCd.jancd = this.FTOT00101Store.JANCD;
      if (!Ultility.isEmpty(this.optionlst)) {
        this.optionlsttemp1 = this.optionlst.split(",");
      } else {
        this.optionlsttemp1 = [];
      }
      this.itemrnm = this.FTOT00101Store.ITEMRNM;
      this.pckordqty = this.FTOT00101Store.PCKORDQTY;
      this.URL = this.FTOT00101Store.URL;
      if (this.FTOT00101Store.SCREENPCK == "1") {
        this.storePckrslqty = parseInt(this.FTOT00101Store.PCKRSLTQTY_B);
        this.storeQty = 1;
      } else {
        this.storePckrslqty = parseInt(this.FTOT00101Store.PCKRSLTQTY);
        this.storeQty = 1;
      }
      this.labelPckrsltqty = this.storeQty + this.storePckrslqty;

      if (this.FTOT00101Store.WRAPPINGFLG == 1 || this.FTOT00101Store.HEMMINGFLG == 1) {
        this.optionColor = true;
      }
    } else {
      this.fnGetData();
    }
    this.incode.focusIn();
  }

  fnDestroy() {
  }


  // click link item
  btnClickLink(URL: any) {
    window.open(URL)
  }

  btnBack() {
    this.FTOT00101Store.ITEMCD = "";
    this.FTOT00101Store.LotDispFlg = "";
    this.FTOT00101Store.PCKNGLSTNO = "";
    this.router.navigate([DisplayScreenID.STOT00103])
  }

  // check before confirm
  async btnConfirm() {
    let a: any;
    if (!Ultility.isEmpty(this.incode.getVal())) {
      a = await this.fnScan();
      if (a == 1) {
        this.incode.focusIn();
        return;
      } else {
        this.btnRegist();
      }
    } else {
      this.btnRegist();
    }
  }


  // regist
  btnRegist() {
    if ((this.labelPckrsltqty < parseInt(this.FTOT00101Store.PCKORDQTY) && this.labelPckrsltqty == 0) || (this.storePckrslqty == this.labelPckrsltqty && this.labelPckrsltqty < parseInt(this.FTOT00101Store.PCKORDQTY))) {
      var msg = this.messageEntity.message["ME000012"].replace("%1", this.formItemNm[2]);
      this.notifyError(this.appService, msg, () => {
        this.incode.focusIn();
        this.incode.selectAll();
      })
      return;
    }

    var CSTMITEMGRPCD = ""
    if (this.FTOT00101Store.FLGPCK == "0") {
      if (this.FTOT00101Store.TYPEKEY == "K") {
        CSTMITEMGRPCD = "KIDS";
      } else if (this.FTOT00101Store.TYPEKEY == "H") {
        CSTMITEMGRPCD = "HOME";
      } else if (this.FTOT00101Store.TYPEKEY == "F") {
        CSTMITEMGRPCD = "FASHION";
      } else if (this.FTOT00101Store.TYPEKEY == "G") {
        CSTMITEMGRPCD = "BEAUTY";
      }
    }

    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "FLGPCK": this.FTOT00101Store.FLGPCK,
        "SCREENPCK": this.FTOT00101Store.SCREENPCK,
        "TRFLG": this.FTOT00101Store.TRFLG,
        "SOORDNO": this.FTOT00101Store.SOORDNO,
        "ITEMCD": this.FTOT00101Store.ITEMCD,
        "LIMITDATE": this.FTOT00101Store.LIMITDATE,
        "STCKMNGKEY1": this.FTOT00101Store.STCKMNGKEY1,
        "STCKMNGKEY2": this.FTOT00101Store.STCKMNGKEY2,
        "STCKMNGKEY3": this.FTOT00101Store.STCKMNGKEY3,
        "SODTLNO": this.FTOT00101Store.SODTLNO,
        "LCTNCD": this.FTOT00101Store.LCTNCD,
        "PCKQTY": this.storeQty,
        "LOTDISPFLG": this.FTOT00101Store.LotDispFlg,
        "HOLDQTY": this.storeQty,
        "QLTYCD": this.FTOT00101Store.QLTYCD,
        "PCKNGLSTNO": this.FTOT00101Store.PCKNGLSTNO,
        "CSTMITEMGRPCD": CSTMITEMGRPCD,
        "FLGCHECKSOORDNO": this.FTOT00101Store.FLGCHECKSOORDNO
      }
    }

    return this.webService.callWs(Const.stot00105RegistWs, request, async (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.incode.focusIn();
        })
        if (response.BACKSCR == "STOT00102") {
          this.incode.setVal("");
          this.router.navigate([response.BACKSCR]);
        }
        return;
      } else {
        this.FTOT00101Store.FLGSCAN = "";
        // await this.fnGetData();
        if (response.BACKSCR == "STOT00102") {
          if (response.pCnt == "0") {
            if (response.LCTNFLG == "1") {
              this.notifySuccess(this.appService, this.messageEntity.message["MI009998"], () => {
                this.incode.setVal("");
                this.incode.focusIn();
              })
            } else {
              this.notifySuccess(this.appService, this.messageEntity.message["MI009997"], () => {
                this.incode.setVal("");
                this.incode.focusIn();
              })
            }
          } else {
            if (response.LCTNFLG == "1") {
              this.notifySuccess(this.appService, this.messageEntity.message["MI009998"], () => {
                this.incode.setVal("");
                this.incode.focusIn();
              })
            } else {
              this.incode.setVal("");
              this.incode.focusIn();
            }
          }
          if (response.BACKSCR == "STOT00102" && this.FTOT00101Store.TRFLG == "0" && response.LCTNFLG == "0") {
            this.router.navigate([DisplayScreenID.STCM00203]);
          } else {
            this.router.navigate([response.BACKSCR]);
          }
        } else if (response.BACKSCR == "STOT00105") {
          //this.notifySuccess(this.appService, this.messageEntity.message["MI000008"], () => {
          this.incode.setVal("");
          this.incode.focusIn();
          //})
          this.fnGetData();
          return;
        } else {
          //this.notifySuccess(this.appService, this.messageEntity.message["MI000008"], () => {
          if (response.BACKSCR == "STOT00101" && this.FTOT00101Store.FLGCHECKSOORDNO == "1") {
            if (response.LCTNFLG == "1") {
              this.notifySuccess(this.appService, this.messageEntity.message["MI009998"], () => {
                this.incode.setVal("");
                this.incode.focusIn();
              })
            } else {
              this.notifySuccess(this.appService, this.messageEntity.message["MI009997"], () => {
                this.incode.setVal("");
                this.incode.focusIn();
              })
            }
          } else {
            this.incode.setVal("");
            this.incode.focusIn();
          }
          //})

          if (response.BACKSCR == "STOT00101" && this.FTOT00101Store.TRFLG == "0" && response.LCTNFLG == "0") {
            this.router.navigate([DisplayScreenID.STCM00203]);
          } else {
            this.router.navigate([response.BACKSCR]);
          }

        }
      }
    })
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }


  //func click enter 
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'incode':
        this.fnScan();
        break;
    }
  }

  //click 
  btnShortage() {
    this.router.navigate([DisplayScreenID.STOT00108]);
  }

  btnNext() {
    this.btnConfirm();
  }

  // init load data
  async fnGetData() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "SCREENPCK": this.FTOT00101Store.SCREENPCK,
        "TRFLG": this.FTOT00101Store.TRFLG,
        "SOORDNO": this.FTOT00101Store.SOORDNO,
        "ITEMCD": this.FTOT00101Store.ITEMCD,
        "LCTNCD": this.FTOT00101Store.LCTNCD1
      }
    }
    return await this.webService.callWs(Const.stot00105GetDataWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.incode.focusIn();
          this.incode.setVal("");
        })
        return;
      } else {
        this.storeCd.itemcd = this.FTOT00101Store.ITEMCD;
        this.FTOT00101Store.TRFLG = response.stot00105DataDto.TRFLG;
        this.jancd = response.stot00105DataDto.JANCD;
        this.color = response.stot00105DataDto.COLOR;
        this.size = response.stot00105DataDto.SIZE;
        this.itemnm = response.stot00105DataDto.ITEMNM;
        this.itemrnm = response.stot00105DataDto.ITEMRNM;
        this.pckordqty = response.stot00105DataDto.PCKORDQTY;
        this.optionlst = response.stot00105DataDto.OPTIONLST;
        if (!Ultility.isEmpty(this.optionlst)) {
          this.optionlsttemp1 = this.optionlst.split(",");
        } else {
          this.optionlsttemp1 = [];
        }
        this.URL = response.stot00105DataDto.URL;
        this.FTOT00101Store.INSTRCD = response.stot00105DataDto.INSTRCD;
        this.storeCd.instck = response.stot00105DataDto.INSTRCD;
        this.storeCd.jancd = response.stot00105DataDto.JANCD;
        this.FTOT00101Store.LCTNCD = response.stot00105DataDto.LCTNCD;
        this.FTOT00101Store.QLTYCD = response.stot00105DataDto.QLTYCD;
        this.FTOT00101Store.PCKNGLSTNO = Ultility.isEmpty(response.stot00105DataDto.PCKNGLSTNO) ? '' : response.stot00105DataDto.PCKNGLSTNO;
        this.FTOT00101Store.JANCD = response.stot00105DataDto.JANCD;
        this.FTOT00101Store.COLOR = response.stot00105DataDto.COLOR;
        this.FTOT00101Store.SIZE = response.stot00105DataDto.SIZE;
        this.FTOT00101Store.ITEMNM = response.stot00105DataDto.ITEMNM;
        this.FTOT00101Store.PCKORDQTY = response.stot00105DataDto.PCKORDQTY;
        this.FTOT00101Store.OPTIONLST = response.stot00105DataDto.OPTIONLST;
        this.FTOT00101Store.PCKRSLTQTY = response.stot00105DataDto.PCKRSLTQTY;
        this.FTOT00101Store.PCKRSLTQTY_B = response.stot00105DataDto.PCKRSLTQTY_B;
        this.FTOT00101Store.SODTLNO = response.stot00105DataDto.SODTLNO;
        this.FTOT00101Store.LIMITDATE = Ultility.isEmpty(response.stot00105DataDto.LIMITDATE) ? '' : response.stot00105DataDto.LIMITDATE;
        this.FTOT00101Store.STCKMNGKEY1 = Ultility.isEmpty(response.stot00105DataDto.STCKMNGKEY1) ? '' : response.stot00105DataDto.STCKMNGKEY1;
        this.FTOT00101Store.STCKMNGKEY2 = Ultility.isEmpty(response.stot00105DataDto.STCKMNGKEY2) ? '' : response.stot00105DataDto.STCKMNGKEY2;
        this.FTOT00101Store.STCKMNGKEY3 = Ultility.isEmpty(response.stot00105DataDto.STCKMNGKEY3) ? '' : response.stot00105DataDto.STCKMNGKEY3;

        if (this.FTOT00101Store.SCREENPCK == "1") {
          this.storePckrslqty = parseInt(this.FTOT00101Store.PCKRSLTQTY_B);
        } else {
          this.storePckrslqty = parseInt(this.FTOT00101Store.PCKRSLTQTY);
        }
        if (this.FTOT00101Store.FLGSCAN == 1) {
          this.storeQty = 1;
        } else {
          this.storeQty = 0;
        }
        this.labelPckrsltqty = this.storeQty + this.storePckrslqty;

        if (response.stot00105DataDto.WRAPPINGFLG == 1 || response.stot00105DataDto.HEMMINGFLG == 1) {
          this.optionColor = true;
        }
        this.FTOT00101Store.DnpyoDetailList = response.stot00105DataDto;
      }
    })
  }

  //check incode
  fnScan() {
    this.incodeMessage = "";
    var incode = this.incode.getVal();
    var flgExitScan = 0;
    if (Ultility.isEmpty(this.incode.getVal())) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000116"];
      flgExitScan = 1;
      return flgExitScan;
    }

    var beforeStoreQty = this.storeQty
    if (Ultility.isEmpty(incode) == false && ((!Ultility.isEmpty(this.storeCd.itemcd) && (incode.toUpperCase() == this.storeCd.itemcd.toUpperCase())) || (!Ultility.isEmpty(this.storeCd.instck) && (incode.toUpperCase() == this.storeCd.instck.toUpperCase())) || (!Ultility.isEmpty(this.storeCd.jancd) && (incode.toUpperCase() == this.storeCd.jancd.toUpperCase())))) {
      this.storeQty = this.storeQty + 1;
      this.labelPckrsltqty = this.storeQty + this.storePckrslqty;
      if (parseInt(this.FTOT00101Store.PCKORDQTY) - this.labelPckrsltqty < 0) {
        this.storeQty = beforeStoreQty;
        this.labelPckrsltqty = this.storeQty + this.storePckrslqty;
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        var msg = this.message.message["ME000998"];
        this.notifyError(this.appService, msg, () => {
          this.incode.focusIn();
          this.incode.setVal("");
        })
        flgExitScan = 1;
        return flgExitScan;
      } else {
        Ultility.handyCtrl(this.ledService, Const.LedGreen, true, Const.SoundSuccess);
        this.labelPckrsltqty = this.storeQty + this.storePckrslqty;
        if (this.FTOT00101Store.SCREENPCK == "1") {
          this.FTOT00101Store.DnpyoDetailList.PCKRSLTQTY_B = this.labelPckrsltqty;
        } else {
          this.FTOT00101Store.DnpyoDetailList.PCKRSLTQTY = this.labelPckrsltqty;
        }
        this.incode.focusIn();
        this.incode.setVal("");
        return flgExitScan;
      }
    }

    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "INDEX": this.FTOT00101Store.INDEX,
        "SCANCD": incode,
        "SCREENPCK": this.FTOT00101Store.SCREENPCK
      },
      "stot0010DnpyoDetailList": this.FTOT00101Store.DnpyoDetailList
    }
    this.webService.callWs(Const.stot00105ScanSuryoCountInstockWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.incode.setVal("");
          this.incode.focusIn();
        })
        flgExitScan = 1;
        return flgExitScan;
      } else {
        Ultility.handyCtrl(this.ledService, Const.LedGreen, true, Const.SoundSuccess);
        if (response.CHECKFLG == true) {
          if (this.FTOT00101Store.PCKORDQTY < response.INCODE) {
            Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
            var msg = this.message.message["ME000998"];
            this.notifyError(this.appService, msg, () => {
              this.incode.focusIn();
              this.incode.setVal("");
            })
            flgExitScan = 1;
            return flgExitScan;
          } else if (this.storePckrslqty > response.INCODE) {
            this.incode.focusIn();
            this.incode.setVal("");
          } else {
            this.labelPckrsltqty = response.INCODE;
            if (this.FTOT00101Store.SCREENPCK == "1") {
              this.FTOT00101Store.DnpyoDetailList.PCKRSLTQTY_B = response.INCODE;
            } else {
              this.FTOT00101Store.DnpyoDetailList.PCKRSLTQTY = response.INCODE;
            }
            this.storeQty = response.INCODE - this.storePckrslqty;
            this.incode.focusIn();
            this.incode.setVal("");
          }
        } else {
          if (this.FTOT00101Store.SCREENPCK == "1") {
            this.FTOT00101Store.DnpyoDetailList.PCKRSLTQTY_B = response.stot0010dnpyoDetail.PCKRSLTQTY_B;
            this.labelPckrsltqty = response.stot0010dnpyoDetail.PCKRSLTQTY_B;
            this.storeQty = response.stot0010dnpyoDetail.PCKRSLTQTY_B - this.storePckrslqty;
            this.incode.setVal("");
            this.incode.focusIn();
          } else {
            this.FTOT00101Store.PCKRSLTQTY = response.stot0010dnpyoDetail.PCKRSLTQTY;
            this.FTOT00101Store.DnpyoDetailList.PCKRSLTQTY = response.stot0010dnpyoDetail.PCKRSLTQTY;
            this.labelPckrsltqty = response.stot0010dnpyoDetail.PCKRSLTQTY;
            this.storeQty = response.stot0010dnpyoDetail.PCKRSLTQTY - this.storePckrslqty;
            this.incode.setVal("");
            this.incode.focusIn();
          }
        }
      }
      return flgExitScan;
    })

    return flgExitScan;
  }

}
