import { Component, Input, OnInit, ElementRef, Output, EventEmitter, Injectable, ViewChild } from '@angular/core';
import * as Const from 'src/app/common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { NgbDateParserFormatter, NgbDate, NgbDateStruct, NgbCalendar } from '@ng-bootstrap/ng-bootstrap';
import * as moment from 'moment';

@Component({
  selector: 'app-input-date',
  templateUrl: './input-date.component.html'
})
export class InputDateComponent implements OnInit {
  @ViewChild('ngbDatepicker') ngbDatepicker: ElementRef | any;
  constructor(private el: ElementRef, private calendar: NgbCalendar) { }
  // Define input mode: none, latin, number ...
  inputmode: string = Const.InputMode.NONE;

  // Input model: ngModel angular
  inputModel: any;

  // Input Id
  inputId: any

  // Parameter from parent
  @Input() inputType: any;
  @Input() isPassword: any = '0';

  // Emit function
  @Output() inputKeyEnt = new EventEmitter();
  @Output() inputBlurEvent = new EventEmitter();
  @Output() inputFocusEvent = new EventEmitter();

  ngOnInit(): void {
    this.inputId = this.el.nativeElement.id;
  }

  onDateSelect(evt: any) {
    this.inputModel = evt;
  }

  /******************************************
   * SHOW HIDE KEYBOARD ON FOCUS INPUT
   ******************************************/
  keyboardClick = () => {
    if (this.inputmode == Const.InputMode.NONE) {
      this.inputmode = this.inputType;

      var elm = this.el.nativeElement as HTMLInputElement;
      var input = elm.children[0].firstChild as HTMLInputElement;
      input.focus();
    }
  }

  /******************************************
   * SET INPUTMODE TO 'NONE' WHEN FOCUS OUT
   ******************************************/
  inputBlur = () => {
    this.inputmode = Const.InputMode.NONE;

    this.inputBlurEvent.emit();
  }

  inputFocus = () => {
    this.inputFocusEvent.emit();
  }

  /******************************************
   * SET VALUE FROM PARENT TO INPUT
   ******************************************/
  setVal(val: any) {
    if (Ultility.isEmpty(val)) {
      this.inputModel = "";
      return;
    }

    if (val.indexOf("-") != -1) {
      var tmpDay = val.split("-");
      this.inputModel = {
        "year": parseInt(tmpDay[0]),
        "month": parseInt(tmpDay[1]),
        "day": parseInt(tmpDay[2])
      }
      return;
    }

    if (val.indexOf("/") != -1) {
      var tmpDay = val.split("/");
      this.inputModel = {
        "year": parseInt(tmpDay[0]),
        "month": parseInt(tmpDay[1]),
        "day": parseInt(tmpDay[2])
      }
      return;
    }
  }

  setValDefalt() {
    let dtTemp = moment().format('YYYY/MM/DD');
    this.setVal(dtTemp);
  }

  /******************************************
   * GET INPUT CURRENT VALUE
   ******************************************/
  getVal() {
    if (typeof this.inputModel === "object" && this.inputModel != null) {
      var year = this.inputModel.year;
      var monthTemp = this.inputModel.month.toString();
      var month = monthTemp.length < 2 ? "0" + monthTemp : monthTemp;
      var dayTemp = this.inputModel.day.toString();
      var day = dayTemp.length < 2 ? "0" + dayTemp : dayTemp;
      return `${year}-${month}-${day}`;
    }

    if (typeof this.inputModel === "string" && Ultility.isEmpty(this.inputModel) == false) {
      if (moment(this.inputModel, "YYYY/MM/DD", true).isValid()) {
        return moment(this.inputModel, "YYYY/MM/DD").format('YYYY-MM-DD');
      }

      if (moment(this.inputModel, "YYYY-MM-DD", true).isValid()) {
        return this.inputModel;
      }

      if (moment(this.inputModel, "YYYYMMDD", true).isValid()) {
        return moment(this.inputModel, "YYYYMMDD").format('YYYY-MM-DD');
      }

      return this.inputModel;

    } else {
      return "";
    }
  }

  /******************************************
   * SET FOCUSIN INPUT
   ******************************************/
  focusIn() {
    var elm = this.el.nativeElement as HTMLInputElement;
    var input = elm.children[0].firstChild as HTMLInputElement;
    input.focus();
  }
  selectAll() {
    var elm = this.el.nativeElement as HTMLInputElement;
    var input = elm.children[0].firstChild as HTMLInputElement;
    input.select();
  }

  /******************************************
   * EMIT WHEN ENT PRESS
   ******************************************/
  inputKeyEntFunc = () => {
    this.inputKeyEnt.emit();
  }

  fnTodayClick() {
    this.inputModel = this.calendar.getToday();
    this.ngbDatepicker.close();
  }

  fnClearClick() {
    this.inputModel = null;
    this.ngbDatepicker.close();
    this.focusIn();
  }

  fnCloseClick() {
    this.ngbDatepicker.close();
    this.focusIn();
  }
}


function padNumber(value: number) {
  if (!isNaN(value) && value !== null) {
    return `0${value}`.slice(-2);
  }
  return '';
}

@Injectable()
export class NgbDateCustomParserFormatter extends NgbDateParserFormatter {
  parse(value: string): NgbDateStruct | null {
    if (value) {
      var dateParts = value.trim().split('-');

      let dateObj: NgbDateStruct = { day: parseInt(dateParts[2]), month: parseInt(dateParts[1]), year: parseInt(dateParts[0]) }
      return dateObj;
    }
    return null;
  }

  static formatDate(date: NgbDateStruct | NgbDate | null): string {
    return date ?
      `${date.year || ''}/${padNumber(date.month)}/${padNumber(date.day)}` :
      '';
  }

  format(date: NgbDateStruct | null): string {
    return NgbDateCustomParserFormatter.formatDate(date);
  }
}