import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0060StoreService } from 'src/app/services/FTOT0060/ftot0060-store.service';

@Component({
  selector: 'app-stot00603',
  templateUrl: './stot00603.component.html'
})
export class STOT00603Component extends BaseComponent implements OnInit {


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0060Store: FTOT0060StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00603;

  lstDeliMethod: any = [];
  CSTMRSVNO: string = "";
  fnInit() {
    this.CSTMRSVNO = this.FTOT0060Store.CSTMRSVNO;
    this.fnGetData();
  }

  fnDestroy() {
  }

  btnClickMethod(DELIMTHDCD: any) {
    this.FTOT0060Store.DELIMTHODCD = DELIMTHDCD;
    if (DELIMTHDCD == "1002" || DELIMTHDCD == "1010") {
      this.router.navigate([DisplayScreenID.STOT00604]);
    }
    //
    else if (DELIMTHDCD == "1009") {
      this.FTOT0060Store.DELIBOXQTY = 1;
      this.FTOT0060Store.DELIBOXITEMCD = "";
      if (this.FTOT0060Store.SCMFLG == "1" && (this.FTOT0060Store.DELIMTHDCD == "1101" || this.FTOT0060Store.DELIMTHDCD == "1103")) {
        this.UpdDenpyou(2);
      } else {
        this.UpdDenpyou(3);
      }
    }
  }

  UpdDenpyou(modeFlg: any) {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00208),
      "stot0060ConditionDto": {
        "SOORDNO": this.FTOT0060Store.SOORDNO,
        "HHTIP": this.FTOT0060Store.HHTIP,
        "COMPUTERCD": this.FTOT0060Store.COMPUTERCD,
        "PRINTERCD": this.FTOT0060Store.PRINTERCD,
        "DELIMTHDSCMCD": this.FTOT0060Store.DELIMTHDCD,
        "DELIMTHDCD": this.FTOT0060Store.DELIMTHODCD,
        "PAGENUM": this.FTOT0060Store.DELIBOXQTY,
        "SYSKBN1": this.getSystemEntity("SYSKBN1"),
        "MODEFLG": modeFlg,
        "DELIBOXQTY": this.FTOT0060Store.DELIBOXQTY,
        "DELIBOXITEMCD": this.FTOT0060Store.DELIBOXITEMCD,
      }
    }
    return this.webService.callWs(Const.stot00605UpdDenpyouWs, request, async (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          return;
        });
      } else {
        this.notifySuccess(this.appService, this.messageEntity.message["MI000008"], () => {
          this.FTOT0060Store.clear();
          this.router.navigate([DisplayScreenID.STOT00601])
        })
      }
    });
  }

  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00602]);
  }

  //get init data
  fnGetData() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0060ConditionDto": {
        "SOORDNO": this.FTOT0060Store.SOORDNO,
      }
    }
    return this.webService.callWs(Const.stot00603GetDataWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
        })
        return;
      } else {
        this.lstDeliMethod = response.lstDeliMethod;
      }
    })
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

}
