import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0040StoreService } from 'src/app/services/FTOT0040/ftot0040-store.service';

@Component({
  selector: 'app-stot00405',
  templateUrl: './stot00405.component.html'
})
export class STOT00405Component extends BaseComponent implements OnInit {


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0040STORE: FTOT0040StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00405;
  deliBoxQtyMessage: string = "";
  incodeMessage: string = "";
  @ViewChild("deliBoxQty") deliBoxQty: InputComponent | any;
  @ViewChild("incode") incode: InputComponent | any;
  delimthcd = "";
  CSTMRSVNO: String = "";
  delimthcds: any = [];

  fnInit() {
    this.incode.setVal(Ultility.isEmpty(this.FTOT0040STORE.listShipment[this.FTOT0040STORE.NowIndex].INQNO) ? "" : this.FTOT0040STORE.listShipment[this.FTOT0040STORE.NowIndex].INQNO);
    this.fnGetData();
  }

  fnGetData() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00405)
    }

    return this.webService.callWs(Const.stot00405GetDataWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.deliBoxQty.focusIn();
          this.deliBoxQty.setVal("");
        })
        return;
      } else {
        this.deliBoxQty.focusIn();
        this.delimthcds = response.listDeliMethod;
        this.delimthcd = response.listDeliMethod[0].DELIMTHDCD;
      }
    })
  }

  fnDestroy() {
  }

  //func click enter 
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case "deliBoxQty":
        if (this.fncheckdeliBox(this.deliBoxQty.getVal()) == true) {
          this.deliBoxQty.focusIn();
          return;
        } else {
          this.incode.focusIn();
        }
        break;
      case "incode":
        this.fncheckInqno(this.incode.getVal());
        break;
    }
  }

  fncheckdeliBox(deliBox: any) {
    this.deliBoxQtyMessage = "";
    var hasError = false;
    if (Ultility.isEmpty(deliBox)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.deliBoxQtyMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[2]);
      this.deliBoxQty.focusIn();
      hasError = true;
    } else if (!Ultility.isInteger(deliBox)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.deliBoxQtyMessage = this.message.message["ME000007"].replace("%1", this.formItemNm[2]);
      this.deliBoxQty.focusIn();
      this.deliBoxQty.selectAll();
      hasError = true;
    } else if (deliBox < 0) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.deliBoxQtyMessage = this.message.message["ME000129"];
      this.deliBoxQty.focusIn();
      this.deliBoxQty.selectAll();
      hasError = true;
    } else if (deliBox.length > 9) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000050"].replace("%1", 9);
      this.incode.focusIn();
      this.incode.selectAll();
      hasError = true;
    }
    this.FTOT0040STORE.DELIBOXQTY = deliBox;
    return hasError;
  }

  fncheckInqno(incode: any) {
    this.incodeMessage = "";
    var hasError = false;
    if (Ultility.isEmpty(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[3]);
      this.incode.focusIn();
      hasError = true;
    } else if (!Ultility.isInteger(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000007"].replace("%1", this.formItemNm[2]);
      this.incode.focusIn();
      this.incode.selectAll();
      hasError = true;
    } else if (incode < 0) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000129"];
      this.incode.focusIn();
      this.incode.selectAll();
      hasError = true;
    } else {
      var request = {
        "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00208),
        "stot0040Condition": {
          "INQNO": Ultility.isEmpty(incode) ? "" : incode,
        }
      }
      this.webService.callWs(Const.stot00405CheckInqnoWs, request, (response) => {
        if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.incodeMessage = response.fatalError[0].errMsg;
          this.incode.focusIn();
          this.incode.selectAll();
          hasError = true;
        } else {
          if (response.CHECKINQNO > 0) {
            hasError = true;
          }
        }
      })
    }
    return hasError;
  }

  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00403]);
  }

  btnConFirm() {
    var SOORDNO = this.FTOT0040STORE.listShipment[0].SOORDNO;
    var PCKNGLSTNO = this.FTOT0040STORE.listShipment[0].PCKNGLSTNO;
    var incode = this.incode.getVal();
    if (this.fncheckdeliBox(this.deliBoxQty.getVal()) == true) {
      this.deliBoxQty.focusIn();
      return;
    }
    if (this.fncheckInqno(incode) == true) {
      this.incode.focusIn();
      return;
    }
    if (Ultility.isEmpty(this.delimthcd)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      var msg = this.message.message["ME000014"].replace("%1", this.formItemNm[1]);
      this.notifyError(this.appService, msg, () => {
        this.incode.focusIn();
      })
      return;
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00208),
      "stot0040UpdDnpyo": {
        "SOORDNO": SOORDNO,
        "PCKNGLSTNO": PCKNGLSTNO,
        "SYSKBN1": this.getSystemEntity("SYSKBN1"),
        "INQNO": Ultility.isEmpty(incode) ? "" : incode,
        "REGSTINVCFLG": 0,
        "DELIBOXQTY": this.FTOT0040STORE.DELIBOXQTY,
        "DELIMTHDCD": this.delimthcd
      }
    }
    return this.webService.callWs(Const.stot00405UpdDenpyouWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.deliBoxQty.focusIn();
          this.deliBoxQty.selectAll();
        })
        return;
      } else {
        this.notifySuccess(this.appService, this.messageEntity.message["MI000008"], () => {
          this.router.navigate([DisplayScreenID.STOT00406])
        })
      }
    })
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }



}
