import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { loginItemNm } from 'src/app/common/DefaultItemnm';
import * as Const from 'src/app/common/Const';
import { WebService } from 'src/app/services/common/webservice.service';
import { IObjectString } from 'src/app/common/IObject';
import { BaseComponent } from '../../base/base.component';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { Ultility } from 'src/app/services/common/ultility.service';
import { AuthKbnService } from 'src/app/services/common/authkbn.service';
import { MessageService } from 'src/app/services/common/message.service';
import { Router } from '@angular/router';
import { LedService } from 'src/app/services/common/led.service';
import { AppService } from 'src/app/services/common/app.service';
import { InputComponent } from '../../base/input/input-text.component';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { FTCM0020StoreService } from 'src/app/services/FTCM0020/ftcm0020-store.service';

@Component({
  selector: 'app-stcm00101',
  templateUrl: './stcm00101.component.html'
})
export class STCM00101Component extends BaseComponent implements OnInit {
  @ViewChild("usrcd") usrcd: InputComponent | any;
  @ViewChild("password") password: InputComponent | any;
  @ViewChild("shcm00101") shcm00101: STCM00101Component | any;

  constructor(protected loginInfo: LoginInfoService,
    private FTCM0020Store: FTCM0020StoreService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private authkbn: AuthKbnService,
    private appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  // Define variable
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STCM00101;
  formItemNm: IObjectString = {};
  usrcdMessage: String = "";
  passwordMessage: String = "";

  ngOnInit(): void {
    this.fnInit(false);
  }
  /**
    * 
    */
  fnInit(isMenu: boolean) {
    // Set formItemName
    this.formItemNm[0] = loginItemNm[Const.langType].user;
    this.formItemNm[1] = loginItemNm[Const.langType].title;
    this.formItemNm[2] = loginItemNm[Const.langType].brnch;
    this.formItemNm[3] = loginItemNm[Const.langType].lang;
    this.formItemNm[4] = loginItemNm[Const.langType].password;
    this.formItemNm[5] = loginItemNm[Const.langType].loginbtn;

    // Hidden menu
    var data: IObjectString = {};
    data["SCREENTITLE"] = this.formItemNm[1];
    data["HEADERDISPLAY"] = false;
    this.appService.toggle(data);

    // Set focus
    setTimeout(() => {
      this.usrcd.focusIn();
      this.usrcd.selectAll();
    })
  }
  fnUsrcdEnt() {
    this.password.focusIn();
    this.password.selectAll();
  }
  /********************************
  * Login process
  ********************************/
  btnLoginClick() {
    this.fnClearErrorMsg();

    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STCM00101),
      "stcm00101LoginCondition": {
        "USRCD": this.usrcd.getVal(),
        "PASSWORD": this.password.getVal()
      }
    }
    this.webService.callWs(Const.stcm00101LoginWs, request, async (response) => {
      // 
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.usrcd.focusIn();
        this.usrcd.selectAll();
        return;
      } else {
        Ultility.handyCtrl(this.ledService, Const.LedGreen, true, Const.SoundSuccess);
      }
      // Update loginInfo
      this.loginInfo.usercd = response.stcm00101LoginRow.USRCD;
      this.loginInfo.usernm = response.stcm00101LoginRow.USRNM;

      this.loginInfo.cstmcd = response.stcm00101LoginRow.CSTMCD;
      this.loginInfo.cstmnm = response.stcm00101LoginRow.CSTMNM;

      this.loginInfo.brnchcd = response.stcm00101LoginRow.BRNCHCD;
      this.loginInfo.brnchnm = response.stcm00101LoginRow.BRNCHNM;

      this.loginInfo.authgrp = response.stcm00101LoginRow.AUTHGRP;
      this.loginInfo.token = response.stcm00101LoginRow.TOKEN;
      this.loginInfo.key = response.stcm00101LoginRow.KEY;
      this.loginInfo.lang = response.stcm00101LoginRow.LANG;

      var keys = Object.keys(response.stcm00101LoginRow);
      for (var key of keys) {
        this.systemEntity.value[key] = response.stcm00101LoginRow[key];
      }

      // Update authkbn
      if (response.stcm00101FunctionRows) {
        for (const f of response.stcm00101FunctionRows) {
          this.authkbn.authKbnList[f.SCRNID] = f.USETYPE;
        }
      }

      // Update message
      if (response.stcm00101MessageRows) {
        for (const m of response.stcm00101MessageRows) {
          this.message.message[m.MESSAGEID] = m.MESSAGE;
        }
      }

      /**
       * TOP
       */
      this.menu.isInitMenu = true;
      await this.fnGetListCstmcd();
      await this.fnGetListBrnchcd();
      this.fnSetScreen();
    });
  }

  fnSetScreen() {
    if (this.FTCM0020Store.lstBrnchcd.length == 1 && this.FTCM0020Store.lstCstmcd.length == 1) {
      this.FTCM0020Store.BeforeScreenLg = 'STCM00101';
      this.router.navigate([Const.rootingbase + DisplayScreenID.STCM00202]);
    } else if (this.FTCM0020Store.lstBrnchcd.length < 1 && this.FTCM0020Store.lstCstmcd.length <= 1) {
      return;
    } else {
      this.router.navigate([Const.rootingbase + DisplayScreenID.STCM00201]);
    }
  }

  fnClearErrorMsg() {
    this.usrcdMessage = "";
    this.passwordMessage = "";
  }

  // Control input enter key
  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  /**
   * 
   */
  fnDestroy() {

  }

  cstmcd = "";
  lstCstmcd = [];
  mainCstmcd = "";
  brnchcd = "";
  lstBrnchcd = [];
  mainBrnchcd = "";

  /********************************
   * GET LIST CSTMCD
   ********************************/
  async fnGetListCstmcd() {
    if (Ultility.isEmpty(this.menu.storeCstmcd) == false && this.menu.storeListCstmcd.length > 0) {
      this.lstCstmcd = this.menu.storeListCstmcd;
      this.cstmcd = this.menu.storeCstmcd;
      return;
    }

    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STCM00201),
      "tmt020SearchCondition": {
        "FUNCID": "FTCM0010"
      }
    }

    // Get cstmcd
    return await this.webService.callWs(Const.tmt020SearchWs, request, (response) => {
      // 
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        return;
      }
      this.lstCstmcd = response.rows;
      this.menu.storeListCstmcd = response.rows;

      for (let row of response.rows) {
        //BRNCHCD
        if (row["CSTMCD"] == this.loginInfo.cstmcd) {
          this.loginInfo.cstmnm = row["CSTMNM"];
          break;
        }
      }
      this.cstmcd = this.loginInfo.cstmcd;
      this.menu.storeCstmcd = this.loginInfo.cstmcd;
      this.FTCM0020Store.cstmcd = this.cstmcd;
      this.FTCM0020Store.lstCstmcd = this.lstCstmcd;
    });
  }
  /********************************
  * GET LIST BRNCHCD
  ********************************/
  fnGetListBrnchcd() {
    if (Ultility.isEmpty(this.menu.storeBrnchcd) == false && this.menu.storeListBrnchcd.length > 0) {
      this.lstBrnchcd = this.menu.storeListBrnchcd;
      this.brnchcd = this.menu.storeBrnchcd;
      return;
    }

    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STCM00201),
      "tmt120SearchCondition": {
        "FUNCID": "FTCM0010"
      }
    }

    // Get cstmcd
    return this.webService.callWs(Const.tmt120SearchWs, request, (response) => {
      // 
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        return;
      }
      this.lstBrnchcd = response.rows;
      this.menu.storeListBrnchcd = response.rows;

      for (let row of response.rows) {
        //BRNCHCD
        if (row["BRNCHCD"] == this.loginInfo.brnchcd) {
          this.loginInfo.brnchnm = row["BRNCHNM"];
          break;
        }
      }
      this.brnchcd = this.loginInfo.brnchcd;
      this.menu.storeBrnchcd = this.loginInfo.brnchcd;
      this.FTCM0020Store.brnchcd = this.brnchcd;
      this.FTCM0020Store.lstBrnchcd = this.lstBrnchcd;

      var data: IObjectString = {};
      data["LOGOUT"] = this.formItemNm[4];
      data["USRNM"] = this.loginInfo.usernm;
      data["CSTMNM"] = this.loginInfo.cstmnm;
      data["BRNCHNM"] = this.loginInfo.brnchnm;
      data["SCREENTITLE"] = this.formItemNm[0];
      data["HEADERDISPLAY"] = true;
      this.appService.toggle(data);

    });
  }


}
