import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0040StoreService } from 'src/app/services/FTOT0040/ftot0040-store.service';

@Component({
  selector: 'app-stot00404',
  templateUrl: './stot00404.component.html'
})
export class STOT00404Component extends BaseComponent implements OnInit {


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0040STORE: FTOT0040StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00404;
  itemflg: boolean = false;
  limitdateflg: boolean = false;
  stckmngkey1flg: boolean = false;
  stckmngkey2flg: boolean = false;
  stckmngkey3flg: boolean = false;
  existflg: boolean = false;
  listShipment: any = [];

  limitdateMessage: string = "";
  stckmngkey1Message: string = "";
  stckmngkey2Message: string = "";
  stckmngkey3Message: string = "";
  CSTMRSVNO: string = "";
  ITEMCD: string = "";
  @ViewChild("limitdate") limitdate: InputComponent | any;
  @ViewChild("stckmngkey1") stckmngkey1: InputComponent | any;
  @ViewChild("stckmngkey2") stckmngkey2: InputComponent | any;
  @ViewChild("stckmngkey3") stckmngkey3: InputComponent | any;

  fnInit() {
    this.listShipment = this.FTOT0040STORE.listShipment;
    this.CSTMRSVNO = this.listShipment[this.FTOT0040STORE.NowIndex].CSTMRSVNO;
    this.ITEMCD = this.listShipment[this.FTOT0040STORE.NowIndex].ITEMCD;
    this.limitdate.focusIn();
  }

  //func click enter 
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'limitdate':
        this.stckmngkey1.focusIn();
        this.stckmngkey1.selectAll();
        break;
      case 'stckmngkey1':
        this.stckmngkey2.focusIn();
        this.stckmngkey2.selectAll();
        break;
      case 'stckmngkey2':
        this.stckmngkey3.focusIn();
        this.stckmngkey3.selectAll();
        break;
      case 'stckmngkey3':
        this.limitdate.focusIn();
        this.limitdate.selectAll();
        break;
    }
  }

  fnDestroy() {
  }

  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00402]);
  }

  btnNext() {
    for (let i = 0; i < this.listShipment.length; i++) {
      if (this.listShipment[i].ITEMCD == this.ITEMCD) {
        this.itemflg = true;
      } else {
        this.itemflg = false;
      }
      var limitdate = Ultility.isEmpty(this.limitdate.getVal()) ? '' : this.limitdate.getVal();
      var stckmngkey1 = Ultility.isEmpty(this.stckmngkey1.getVal()) ? '' : this.stckmngkey1.getVal();
      var stckmngkey2 = Ultility.isEmpty(this.stckmngkey2.getVal()) ? '' : this.stckmngkey2.getVal();
      var stckmngkey3 = Ultility.isEmpty(this.stckmngkey3.getVal()) ? '' : this.stckmngkey3.getVal();

      if ((Ultility.isEmpty(this.listShipment[i].LIMITDATE) ? '' : this.listShipment[i].LIMITDATE) == limitdate) {
        this.limitdateflg = true;
      }

      if ((Ultility.isEmpty(this.listShipment[i].STCKMNGKEY1) ? '' : this.listShipment[i].STCKMNGKEY1) == stckmngkey1) {
        this.stckmngkey1flg = true;
      }

      if ((Ultility.isEmpty(this.listShipment[i].STCKMNGKEY2) ? '' : this.listShipment[i].STCKMNGKEY2) == stckmngkey2) {
        this.stckmngkey2flg = true;
      }

      if ((Ultility.isEmpty(this.listShipment[i].STCKMNGKEY3) ? '' : this.listShipment[i].STCKMNGKEY3) == stckmngkey3) {
        this.stckmngkey3flg = true;
      }

      if (this.itemflg == true && this.limitdateflg == true && this.stckmngkey1flg == true && this.stckmngkey2flg == true && this.stckmngkey3flg == true) {
        this.existflg = true;
        this.FTOT0040STORE.NowIndex = i;
        break;
      }
    }
    if (this.existflg == false) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      var msg = this.messageEntity.message["ME000115"];
      this.notifyError(this.appService, msg, () => {
        this.limitdate.focusIn();
        this.limitdate.selectAll();
      })
      return;
    } else {
      this.FTOT0040STORE.LotChckFlg = 1;
      this.router.navigate([DisplayScreenID.STOT00403]);
    }
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }





}
