import { Component, OnInit } from '@angular/core';
import { LedService } from 'src/app/services/common/led.service';
import * as Const from '../../../common/Const';

@Component({
  selector: 'app-led',
  templateUrl: './led.component.html'
})
export class LedComponent implements OnInit {
  isShowLedGreen: boolean = false;
  isShowLedRed: boolean = false;

  constructor(private ledService: LedService) {
    // Init
    this.isShowLedGreen = false;
    this.isShowLedRed = false;

    ledService.ledDisplaySub.subscribe((value) => {
      if (value["LEDTYPE"] == Const.LedGreen) {
        // Led display
        this.isShowLedGreen = true;
        // Led hide
        setTimeout(() => {
          this.isShowLedGreen = false;
        }, Const.LedDisplayTime);
      } else if (value["LEDTYPE"] == Const.LedRed) {
        // Led display
        this.isShowLedRed = true;
        setTimeout(() => {
          this.isShowLedRed = false;
        }, Const.LedDisplayTime);
      }


    });
  }

  ngOnInit(): void {
  }

}
