import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { FTIN0110StoreService } from 'src/app/services/FHIN0110/ftin0110-store.service';

@Component({
  selector: 'app-stin01102',
  templateUrl: './stin01102.component.html'
})
export class STIN01102Component extends BaseComponent implements OnInit {
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STIN01102;


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected FTIN01101Store: FTIN0110StoreService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  ITEMCD: string = "";
  ITEMNM:string = "";
  CSTMRSVNO: string = "";

  lctncdMessage: string = "";
  @ViewChild("lctncd") lctncd: InputComponent | any;
  fnInit() {
    this.ITEMCD = this.FTIN01101Store.ITEMCD;
    this.ITEMNM = this.FTIN01101Store.ITEMNM;
    this.CSTMRSVNO = this.FTIN01101Store.CSTMRSVNO;
    this.lctncd.focusIn();
  }

  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    this.fnCheckLctnCd();
  }

  //Check LCTNCD
  fnCheckLctnCd() {
    var lctncd = this.lctncd.getVal();
    if (Ultility.isEmpty(lctncd)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      var msg = this.message.message["ME000004"].replace("%1", this.formItemNm[3]);
      this.notifyError(this.appService, msg, () => {
        this.lctncd.focusIn();
      })
      return;
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stin0110ConditionDtoRequest": {
        "LCTNCD": lctncd,
        "ITEMCD": this.FTIN01101Store.ITEMCD,
        "SHIPFLG": this.FTIN01101Store.SHIPFLG,
        "ECFLG": this.FTIN01101Store.ECFLG,
        "FRLCTNCD": this.FTIN01101Store.FRLCTNCD,
        "ECLCTNCD": this.FTIN01101Store.ECLCTNCD,
        "TRLCTN1CD" : this.FTIN01101Store.TRLCTN1CD,
        "ECLCTN1CD" : this.FTIN01101Store.ECLCTN1CD
      }
    }
    return this.webService.callWs(Const.stin01102CheckLctnCdWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.lctncd.setVal("");
          this.lctncd.focusIn();
        });
        return;
      } else {
        Ultility.handyCtrl(this.ledService, Const.LedGreen, true, Const.SoundSuccess);
        this.FTIN01101Store.LCTNCD = response.stin0110ConditionDtoResponse.LCTNCD;
        this.FTIN01101Store.FRLCTNCD = response.stin0110ConditionDtoResponse.FRLCTNCD;
        this.FTIN01101Store.HOLDRMNQTY = response.stin0110ConditionDtoResponse.HOLDRMNQTY;
        this.router.navigate([DisplayScreenID.STIN01103]);
      }
    })
  }

  btnBack() {
    this.router.navigate([DisplayScreenID.STIN01101]);
  }

  btnNext(){
    this.fnCheckLctnCd();
  }
}
