import { DOCUMENT } from '@angular/common';
import { ChangeDetectorRef, Component, Inject, Injector, Input } from '@angular/core';
import { Router } from '@angular/router';
import { ModalDismissReasons, NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import * as Const from '../app/common/Const';
import { DisplayScreenID } from './common/DisplayScreenId';
import { IObjectString } from './common/IObject';
import { AppService } from './services/common/app.service';
import { LoginInfoService } from './services/common/login-info.service';
import { MessageService } from './services/common/message.service';
import { Ultility } from 'src/app/services/common/ultility.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  isBackMenuDisplay: boolean = false;
  isDisplayMenuHeader: boolean = false;
  cstmnm = "";
  brnchnm = "";
  usrnm = "";
  logout = "";
  screenTitle = "";
  elem: any;
  modalOptions: any;
  constructor(
    private router: Router,
    private appService: AppService,
    private modalService: NgbModal,
    private loginInfo: LoginInfoService,
    private messageService: MessageService,
    @Inject(DOCUMENT) private document: any,
    protected injector: Injector,
    private changeDedectionRef: ChangeDetectorRef
  ) {

    appService.appServiceSub.subscribe((value) => {
      this.isBackMenuDisplay = value["BACKMENUDISPLAY"];
      this.isDisplayMenuHeader = value["HEADERDISPLAY"];
      this.cstmnm = value["CSTMNM"];
      this.brnchnm = value["BRNCHNM"];
      this.usrnm = value["USRNM"];
      this.logout = value["LOGOUT"];
      this.screenTitle = value["SCREENTITLE"];
    });

    appService.appServiceModalSub.subscribe((value) => {
      this.subscribeModal(value);
    });

    this.modalOptions = {
      backdrop: 'static',
      backdropClass: 'customBackdrop', 
      centered : true
    }
  }
  title = 'Brycen Warehouse Management System ( HHT )';
  currentComponent: any;
  /**
   * 
   */
  onActivate(event: any) {
    this.currentComponent = event;
  }
  /**
   * 
   */
  onDeactivate(event: any) {
    this.currentComponent = null;
  }

  ngOnInit() {
    this.elem = document.documentElement;
  }

  fnLogout() {
    var title = "";
    var headerModal = "";
    switch (this.loginInfo.lang) {
      case Const.langVNM:
        title = "Mn hnh ng nhp",
          headerModal = "Xc nhn";
        break;
      case Const.langJPN:
        title = "";
        headerModal = "";
        break;
      case Const.langENG:
        title = "Login";
        headerModal = "Confirm";
        break;
      default:
        title = "";
        headerModal = "";
        break;
    }

    var func = () => {
      this.appService.clear();
      this.screenTitle = title;
      this.isDisplayMenuHeader = false;

      // Clear all store service
      for (const storeService of Const.ListStoreService) {
        this.injector.get(storeService).clear();
      }
      
      this.router.navigate(["/Login"]);
    }

    let modalContext = {
      'style': 'success',
      'type': 'confirm',
      'title': headerModal,
      'content': this.messageService.message["MQ000003"].replace("%1", title),
      'OkFunction': func,
      'cancelFunction': undefined
    }
    this.appService.modal(modalContext);
  }

  modalTitle: string = "";
  modalContent: string = "";
  subscribeModal(modalContext: IObjectString) {
    this.modalTitle = modalContext['title'];
    this.modalContent = modalContext['content'];

    let okFunction = modalContext['OkFunction'];
    let cancelFunction = modalContext['cancelFunction'];

    const modalRef = this.modalService.open(modalNotify, this.modalOptions);
    modalRef.componentInstance.modalContext = modalContext;

    modalRef.result.then((result) => {
      if (result && okFunction != undefined) {
        modalContext = {};
        okFunction();
      } if (result == false && cancelFunction != undefined) {
        modalContext = {};
        cancelFunction();
      }
    }, (reason) => {
      modalContext = {};
      // console.log(this.getDismissReason(reason));
    });
  }


  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
  ngAfterContentChecked(): void {
    this.changeDedectionRef.detectChanges();
  }

  isFullScreen: boolean = false;
  fnToggleFullScreen() {
    this.isFullScreen = !this.isFullScreen;

    if (this.isFullScreen) {
      if (this.elem.requestFullscreen) {
        this.elem.requestFullscreen();
      } else if (this.elem.mozRequestFullScreen) {
        /* Firefox */
        this.elem.mozRequestFullScreen();
      } else if (this.elem.webkitRequestFullscreen) {
        /* Chrome, Safari and Opera */
        this.elem.webkitRequestFullscreen();
      } else if (this.elem.msRequestFullscreen) {
        /* IE/Edge */
        this.elem.msRequestFullscreen();
      }
    } else {
      if (this.document.exitFullscreen) {
        this.document.exitFullscreen();
      } else if (this.document.mozCancelFullScreen) {
        /* Firefox */
        this.document.mozCancelFullScreen();
      } else if (this.document.webkitExitFullscreen) {
        /* Chrome, Safari and Opera */
        this.document.webkitExitFullscreen();
      } else if (this.document.msExitFullscreen) {
        /* IE/Edge */
        this.document.msExitFullscreen();
      }
    }
  }
  fnLinkToMenuClick() {
    var header = "";
    switch (this.loginInfo.lang) {
      case Const.langVNM:
        header = "Menu"
        break;
      case Const.langJPN:
        header = ""
        break;
      case Const.langENG:
        header = "Menu"
        break;
      default:
        header = ""
        break;
    }

    let modalContext = {
      'style': 'success',
      'type': 'confirm',
      'title': header,
      'content': this.messageService.message["MQ000003"].replace("%1", header),
      'OkFunction': () => {
        // Clear all store service
        for (const storeService of Const.ListStoreService) {
          this.injector.get(storeService).clear();
        }
        // 
        this.appService.storedInput = "";
        // Link to menu screen
        this.router.navigate([DisplayScreenID.STCM00202]);
      },
      'cancelFunction': undefined
    }
    this.appService.modal(modalContext);
  }
  fnBodyClick($event: any) {
    let nodeName = $event.target.nodeName && $event.target.nodeName.toLowerCase();
    if (nodeName != "input" && nodeName != "select" && nodeName != "button") {
      if (Ultility.isEmpty(this.appService.storedInput) == false) {
        var input = document.querySelector(`input#${this.appService.storedInput}`) as HTMLElement || document.querySelector(`select#${this.appService.storedInput}`) as HTMLElement;
        input?.focus();
      }
    }
  }
}


@Component({
  template: `
    <div class="modal-header">
        <h1 class="modal-title text-{{modalContext.style == 'success' ? 'label-green' : modalContext.style}}" id="modal-basic-title">{{modalContext.title}}</h1>
        <button type="button" class="btn btn-circle btn-outline-{{modalContext.style}}" (click)="activeModal.dismiss('Cross click')">
            <i class="fa fa-times"></i>
      </button>
    </div>
    <div class="modal-body">
    <h3 [innerHTML]="modalContext.content" class='text-center'></h3>
    </div>
    
    <div class="modal-footer">
        <button ngbAutofocus type="button" id="btnOk" class="btn btn-{{modalContext.style}}" style="width: 80px;" (click)="activeModal.close(true)">OK</button>
        <button type="button" class="btn btn-outline-secondary {{modalContext.type == 'confirm' ? '' : 'd-none'}}" style="width: 80px;" (click)="activeModal.close(false)">Cancel</button>
    </div>
  `
})
export class modalNotify {
  @Input() modalContext: any;

  constructor(public activeModal: NgbActiveModal) {

  }
}
