import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { TableComponent } from '../../base/table/table/table.component';
import { IObjectString } from 'src/app/common/IObject';
import { FTOT0060StoreService } from 'src/app/services/FTOT0060/ftot0060-store.service';

@Component({
  selector: 'app-stot00601',
  templateUrl: './stot00601.component.html'
})
export class STOT00601Component extends BaseComponent implements OnInit {
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00601;


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0060Store: FTOT0060StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  printercdMessage: string = "";
  printercd = "" // 
  ip = ""; //IP
  @ViewChild("printercdview") printercdview: InputComponent | any;

  fnInit() {
    this.fnGetIpPrinter();
  }

  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'printercd':
        this.btnCheckPrinterCD();
        break;
    }
  }

  btnBack() {
    this.backToMenu();
  }

  btnNext() {
    this.btnCheckPrinterCD();
  }

  fnGetIpPrinter() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00601)
    }

    return this.webService.callWs(Const.stot00601GetIpPrinterWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.printercdview.setVal("");
          this.printercdview.focusIn();
        })
        return;
      } else {
        this.FTOT0060Store.HHTIP = response.stot0060ConditionDto.HHTIP;
        this.FTOT0060Store.STRRSRV3 = Ultility.isEmpty(response.stot0060ConditionDto.STRRSRV3) ? "" : response.stot0060ConditionDto.STRRSRV3;
        this.FTOT0060Store.PRINTERCD = Ultility.isEmpty(response.stot0060ConditionDto.PRINTERCD) ? "" : response.stot0060ConditionDto.PRINTERCD;
        this.FTOT0060Store.COMPUTERCD = Ultility.isEmpty(response.stot0060ConditionDto.COMPUTERCD) ? "" : response.stot0060ConditionDto.COMPUTERCD;
        this.ip = response.stot0060ConditionDto.HHTIP;
        let printercd = Ultility.isEmpty(response.stot0060ConditionDto.PRINTERCD) ? "" : response.stot0060ConditionDto.PRINTERCD;
        this.printercdview.setVal(printercd);
        this.printercdview.focusIn();
      }
    })
  }

  btnCheckPrinterCD() {
    this.fnClearMsg();
    if (Ultility.isEmpty(this.printercdview.getVal())) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.printercdMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[2]);
      this.printercdview.focusIn();
      this.printercd = "";
      return;
    }

    let printercd = this.printercdview.getVal();

    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00601),
      "stot0060ConditionDto": {
        "PRINTERCD": printercd,
        "HHTIP": this.FTOT0060Store.HHTIP
      }
    }
    return this.webService.callWs(Const.stot00601CheckPrinterCdWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.printercdview.setVal("");
          this.printercdview.focusIn();
        })
        return;
      } else {
        if (response.UpdPrinterCd == "Success") {
          this.router.navigate([DisplayScreenID.STOT00602]);
        } else {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.printercdMessage = this.message.message["ME000014"].replace("%1", this.formItemNm[2]);
          return;
        }
      }
    })
  }

  fnClearMsg(){
    this.printercdMessage = "";
  }
}
