import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { IObjectString } from 'src/app/common/IObject';

@Injectable({
  providedIn: 'root'
})
export class LedService {
  letDisplay: IObjectString = {};
  ledDisplaySub: Subject<IObjectString> = new Subject<IObjectString>();

  constructor() {
    this.ledDisplaySub.subscribe((value: IObjectString) => {
      this.letDisplay = value;
    });
  }


  DisplayLed(ledTypeObj: IObjectString) {
    this.ledDisplaySub.next(ledTypeObj);
  }
}
