import { Component, Input, OnInit, ElementRef, Output, EventEmitter, Renderer2, ViewChild, QueryList, Injector } from '@angular/core';
import { IObjectString } from 'src/app/common/IObject';
import { Ultility } from 'src/app/services/common/ultility.service';
import { DomSanitizer } from '@angular/platform-browser';
import { DOCUMENT } from '@angular/common';


@Component({
  selector: 'app-table',
  templateUrl: './table.component.html'
})
export class TableComponent implements OnInit {
  constructor(private el: ElementRef,
    private renderer: Renderer2,
    private sanitizer: DomSanitizer,
    protected injector: Injector) {
  }

  // Parameter from parent
  @Input() tableInfo: IObjectString | any;
  @Input() maxHeight: String | any;

  // Emit function
  @Output() rowClick = new EventEmitter();
  @Output() bodyScroll = new EventEmitter();
  @Output() checkboxChange = new EventEmitter();
  @Output() radioChange = new EventEmitter();
  @Output() checkAllChange = new EventEmitter();
  @Output() scrollDown = new EventEmitter();
  //
  @ViewChild('headerArea') headerArea: ElementRef | any;
  @ViewChild('bodyArea') bodyArea: ElementRef | any;

  @ViewChild('headerTemplate') headerTemplate: ElementRef | any;
  @ViewChild('inputHeaderCheckAll') inputHeaderCheckAll: ElementRef | any;
  @ViewChild('bodyTemplate') bodyTemplate: ElementRef | any;

  // PC using flg
  isPC: Boolean = false;

  ngOnInit(): void {

  }
  ngAfterViewInit() {
  }

  fnBuildHeader() {
    var numberOfColumns = parseInt(this.tableInfo.colNum);
    var numberOfRows = parseInt(this.tableInfo.rowSpan);
    var columns = this.tableInfo.columns;

    var headerTemplate = '';
    for (let rowIndex = 0; rowIndex < numberOfRows; rowIndex++) {
      headerTemplate += '<tr>';
      // Build th tag
      if (rowIndex == 0) {
        for (let colIndex = 0; colIndex < numberOfColumns; colIndex++) {
          let columnInfo = columns[colIndex];
          headerTemplate += this.fnCreateThTag(columnInfo);;
        }
        headerTemplate += '</tr>';
      } else {
        for (let colIndex = numberOfColumns; colIndex < columns.length; colIndex++) {
          let columnInfo = columns[colIndex];
          headerTemplate += this.fnCreateThTag(columnInfo);
        }
        headerTemplate += '</tr>';
      }
    }

    this.renderer.setProperty(this.headerTemplate.nativeElement, 'innerHTML', headerTemplate);

    const checkboxList = this.headerTemplate.nativeElement.querySelectorAll("#checkAll");
    for (const checkbox of checkboxList) {
      checkbox.addEventListener('click', ($event: any) => {
        this.fnInputHeaderCheckAll($event);
      });
    }

    // Sync height when multi table display
    const nodeList: Element[] | any = this.injector.get(DOCUMENT).querySelectorAll("table thead");
    if (nodeList.length > 1) {
      let thead = 0;
      for (let node of nodeList) {
        if (thead < node.clientHeight) {
          thead = node.clientHeight;
        }
      }

      for (let node of nodeList) {
        node.style.height = thead + "px";
      }
    }

    // Build max height
    this.maxHeight = `max-height:${this.maxHeight}px`;

    this.headerArea.nativeElement.scrollLeft = 0;

    if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
      const selectorAllFirstTable = document.querySelectorAll("#firsttable");
      selectorAllFirstTable.forEach(selector => selector.setAttribute("style", "overflow:scroll"))
    } else {
      const selectorAllFirstTable = document.querySelectorAll("#firsttable");
      selectorAllFirstTable.forEach(selector => selector.setAttribute("style", "overflow:hidden"))
    }
  }


  fnCheckScrollBar() {
    if (! /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
      this.isPC = true;
      var maxHeight = Ultility.isEmpty(this.bodyArea.nativeElement.style.maxHeight.replace("px", "")) ? 350 : parseInt(this.bodyArea.nativeElement.style.maxHeight.replace("px", ""));
      if (this.bodyArea.nativeElement.scrollHeight < maxHeight) {
        this.isPC = false;
      }
    }
  }

  numberOfColumns: Number | any;
  numberOfRows: Number | any;
  columns: [] | any;
  listData: [] | any;
  direction = "";

  fnBuildBody() {
    this.numberOfColumns = parseInt(this.tableInfo.colNum);
    this.numberOfRows = parseInt(this.tableInfo.rowSpan);
    this.columns = this.tableInfo.columns;
    this.listData = this.tableInfo.data;

    this.headerArea.nativeElement.scrollLeft = 0;
    this.bodyArea.nativeElement.scrollLeft = 0;
  }

  onScrollDown($event: any) {
    this.direction = "scroll down";
    this.scrollDown.emit($event);
  }

  fnCreateThTag(columnInfo: any) {
    let title = columnInfo["title"];
    let rowSpan = Ultility.isEmpty(columnInfo["rowSpan"]) ? "" : `rowspan="${columnInfo["rowSpan"]}"`;
    let width = Ultility.isEmpty(columnInfo["width"]) ? "" : `style="width: ${columnInfo["width"]}"`;
    if (columnInfo["dataType"] == "checkbox") {
      return `<th ${rowSpan} class="text-center align-middle" ${width}><input type="checkbox" #inputHeaderCheckAll name="${columnInfo["colName"]}" id="checkAll"></th>`
    } else
      return `<th ${rowSpan} class="text-center align-middle" ${width}>${title}</th>`;
  }

  fnBuildStyle(columnInfo: any) {
    let style = "";
    // Column width
    if (Ultility.isEmpty(columnInfo["width"]) == false) {
      style += `width: ${columnInfo["width"]}`;
    }

    // Column align
    if (Ultility.isEmpty(columnInfo["align"]) == true) {
      style += `; text-align: left`;
    } else {
      style += `; text-align: ${columnInfo["align"]}`;
    }

    return style;

  }

  // Build data
  fnBuildData(columnInfo: any, data: any, rowData: any) {
    let dataType = columnInfo.dataType;

    if (dataType == "number") {
      return Ultility.fnFormatNumber(data);
    } else if (dataType == "date") {
      return Ultility.fnFormatDate(data);
    } else if (typeof dataType == "function") {
      return dataType(columnInfo, rowData);
    } if (dataType == "concatnate") {

    } else {
      return data;
    }
  }
  fnCheckColumnType(dataType: any) {
    if (typeof dataType == "function") {
      return "function";
    } if (dataType == "concatnate") {
      return "concatnate";
    } if (dataType == "checkbox") {
      return "checkbox";
    } if (dataType == "radio") {
      return "radio";
    } else {
      return "normal";
    }
  }
  // Event
  fnTdClick($event: any, rowIndex: any, rowData: any) {

    if ($event.target.nodeName != "INPUT") {
      var data = {
        event: $event,
        rowInfo: {
          index: rowIndex,
          data: rowData,
        }
      }
      this.rowClick.emit(data);
    }
  }
  fnTableScroll($event: any) {
    // Control scroll header 
    if ($event.srcElement!.scrollLeft > 0) {
      this.headerArea.nativeElement.scrollLeft = $event.srcElement!.scrollLeft;
    } else {
      this.headerArea.nativeElement.scrollLeft = 0;
    }

    // Control body scroll
    if ($event.srcElement!.scrollTop > 0) {
      this.bodyScroll.emit($event);
    }
  }
  fnTableHeaderScroll($event: any) {
    // Control scroll header 
    if ($event.srcElement!.scrollLeft > 0) {
      this.bodyArea.nativeElement.scrollLeft = $event.srcElement!.scrollLeft;
    } else {
      this.bodyArea.nativeElement.scrollLeft = 0;
    }
  }
  // Event
  fnCheckboxChange($event: any, rowIndex: any, colIndex: any, rowData: any, column: any) {

    var data = {
      event: $event,
      rowInfo: {
        column: column,
        rowIndex: rowIndex,
        colIndex: colIndex,
        data: rowData,
      }
    }
    this.checkboxChange.emit(data);
  }

  //Event radio
  fnRadioChange($event: any, rowIndex: any, colIndex: any, rowData: any, column: any) {
    if ((this.tableInfo.data != null && this.tableInfo.data.length > 1) || this.tableInfo.data.length == 0) {
      $event.target.checked = !$event.target.checked;
    }
    var data = {
      event: $event,
      rowInfo: {
        column: column,
        rowIndex: rowIndex,
        colIndex: colIndex,
        data: rowData,
      }
    }
    this.radioChange.emit(data);
  }

  fnInputHeaderCheckAll($event: any) {
    if (this.tableInfo.data == null || this.tableInfo.data.length == 0) {
      $event.target.checked = !$event.target.checked;
      return;
    }
    const isChecked = $event.target.checked;
    const colChecked = $event.target.getAttribute("name");
    for (const row of this.tableInfo.data) {
      row[colChecked] = isChecked;
    }

    this.checkAllChange.emit($event);
  }

  fnSetCheckAll(isCheck: boolean, columnName: string) {
    if (this.tableInfo.data == null || this.tableInfo.data.length == 0) {
      return;
    }
    let index = 0;
    for (const row of this.tableInfo.data) {
      row[columnName] = isCheck;
      let checkbox: any = document.querySelector("#" + columnName + index)!;
      if (checkbox != null) {
        checkbox!.checked = isCheck;
      }
      index++;
    }

    const checkboxList = this.headerTemplate.nativeElement.querySelectorAll("#checkAll");
    for (const checkbox of checkboxList) {
      if (checkbox.getAttribute("name") == columnName) {
        checkbox.checked = isCheck;
        break;
      }
    }
  }

  fnGetIsCheckAll(name: any) {
    const checkboxList = this.headerTemplate.nativeElement.querySelectorAll("#checkAll");
    for (const checkbox of checkboxList) {
      if (checkbox.getAttribute("name") == name) {
        return checkbox.checked;
      }
    }
  }
}