import { Injectable } from '@angular/core';

export class MenuInfo {
  jobKbn!: string;
  jobNm!: string;
  jobNo!: string;
  childs!: ChildMenuInfo[];
}

export class ChildMenuInfo {
  scrnNm!: string;
  scrnId!: string;
}



/**
 * 
 */
@Injectable({
  providedIn: 'root'
})
export class MenuService {
  isMenuCreated = false;
  /**  */
  isInitMenu = false;

  storeCstmcd = "";
  storeBrnchcd = "";

  storeListCstmcd = [];
  storeListBrnchcd = [];
  /** */
  menus = [];
  constructor() { }


  clear() {
    this.storeListCstmcd = [];
    this.storeListBrnchcd = [];
    this.storeCstmcd = "";
    this.storeBrnchcd = "";
    this.isInitMenu = false;
    this.menus = [];
  }
}
