import { Injectable } from '@angular/core';
import * as Const from '../../common/Const';
import { Router } from '@angular/router';
import { isDevMode } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BlockUI } from 'ng-block-ui';
import * as $ from 'jquery';
import { LoginInfoService } from './login-info.service';
import { Ultility } from './ultility.service';
import { AppService } from './app.service';
import { IObjectString } from 'src/app/common/IObject';

@Injectable({
  providedIn: 'root'
})
export class WebService {
  @BlockUI() blockUI: any;
  constructor(
    private router: Router
    , private http: HttpClient
    , private loginInfo: LoginInfoService
    , private appService: AppService,
  ) {
  }
  static readonly SCREEN_MODE_PLUS = 'PLUS';
  static readonly SCREEN_MODE_MINUS = 'MINUS';
  static readonly FILE_TYPE_CSV = 'CSV';
  static readonly FILE_TYPE_PDF = 'PDF';
  static readonly FILE_TYPE_ZIP = 'ZIP';
  /** WebService */
  private webServiceCnt = 0;
  /**  */
  private isWaiting = false;
  /**
   * Web
   * @param serviceNameWeb
   * @param data
   * @param fncSuccessWeb
   * @param fncErrorWeb
   */
  async callWs(serviceName: string,
    data: any,
    fncSuccess?: ((data: any) => void),
    fncError?: (() => void)): Promise<boolean> {

    // waiting
    this.fnWaitScreen(WebService.SCREEN_MODE_PLUS);
    var href = location.href;
    var start = location.origin.length + 1;
    var projectName = href.substring(start, href.indexOf("/", start));

    return $.ajax({
      url: Const.serverHost() + '/' + projectName + '/' + Const.service + serviceName,
      type: 'POST',
      data: JSON.stringify(data),
      dataType: 'json',							// 
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json;charset=utf-8'
      },
    })
      .then((...args: [any, any, any]) => {
        const [data, textStatus, jqXHR] = args;

        // 
        if (this.fnCheckCommonError(data) == false) {
          return false;
        }
        if (fncSuccess != null) {
          fncSuccess(data);
        }
        return true;
      })
      .catch((...args: [any, any, any]) => { // fail
        const [jqXHR, textStatus, errorThrown] = args;

        if (fncError != null) {
          fncError();
        } else {
          this.dispError(textStatus, errorThrown);
        }
        return false;
      })
      .then((value: any) => {
        // waiting
        this.fnWaitScreen(WebService.SCREEN_MODE_MINUS);
        return value;
      });
  }

  async callSilentWs(serviceName: string,
    data: any,
    fncSuccess?: ((data: any) => void),
    fncError?: (() => void)): Promise<boolean> {
    var href = location.href;
    var start = location.origin.length + 1;
    var projectName = href.substring(start, href.indexOf("/", start));
    return $.ajax({
      url: Const.serverHost() + '/' + projectName + '/' + Const.service + serviceName,
      type: 'POST',
      data: JSON.stringify(data),
      dataType: 'json',							// 
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json;charset=utf-8'
      },
    })
      .then((...args: [any, any, any]) => {
        const [data, textStatus, jqXHR] = args;

        // 
        if (this.fnCheckCommonError(data) == false) {
          return false;
        }
        if (fncSuccess != null) {
          fncSuccess(data);
        }
        return true;
      })
      .catch((...args: [any, any, any]) => { // fail
        const [jqXHR, textStatus, errorThrown] = args;

        if (fncError != null) {
          fncError();
        } else {
          this.dispError(textStatus, errorThrown);
        }
        return false;
      })
      .then((value: any) => {
        return value;
      });
  }
  fnGetDisplayText(lang: string) {
    switch (lang) {
      case Const.langJPN:
        return ""
      case Const.langVNM:
        return "Loading"
      case Const.langENG:
        return "Loading"
      default:
        return ""
    }
  }
  /**
   * waiting
   */
  fnWaitScreen(mode: string): void {
    switch (mode) {
      case WebService.SCREEN_MODE_PLUS: this.webServiceCnt = this.webServiceCnt + 1; break;
      case WebService.SCREEN_MODE_MINUS: this.webServiceCnt = this.webServiceCnt - 1; break;
    }

    let displayText: string = "";
    if (Ultility.isEmpty(this.loginInfo.lang)) {
      displayText = this.fnGetDisplayText(Const.langType);
    } else {
      displayText = this.fnGetDisplayText(this.loginInfo.lang);
    }



    // WebServicewaiting
    if (this.webServiceCnt == 1 && !this.isWaiting) {

      this.blockUI.start(displayText);
      this.isWaiting = true;
      // TODO:Enable
    }

    setTimeout(() => {
      // WebServicewaiting
      if (this.webServiceCnt == 0) {
        this.blockUI.stop();
        this.isWaiting = false;
      }
    }, 0);
  }

  /**
   * Web()
   * @param param					
   * @param webServiceNm		Web
   * @param fileType					(CSVPDF)
   */
  callWebServiceForFileDownload(param: string, webServiceNm: string, fileType: string): void {
    // waiting
    this.fnWaitScreen(WebService.SCREEN_MODE_PLUS);

    // 
    switch (fileType) {
      case WebService.FILE_TYPE_CSV: window.location.href = Const.service + webServiceNm + param; break;
      case WebService.FILE_TYPE_ZIP: window.location.href = Const.service + webServiceNm + param; break;
      case WebService.FILE_TYPE_PDF: window.open(Const.service + webServiceNm + param); break;
    }

    // waiting
    this.fnWaitScreen(WebService.SCREEN_MODE_MINUS);
  }


  /**
   * Web()
   * @param serviceNameWeb
   * @param data
   * @param fncSuccessWeb
   * @param fncSuccessErrorWeb
   * @param fncErrorWeb
   */
  async callWebServiceForFileUpload(serviceName: string,
    data: any,
    fncSuccess: (data: any) => void,
    fncError: () => void
  ): Promise<boolean> {



    // waiting
    this.fnWaitScreen(WebService.SCREEN_MODE_PLUS);
    var href = location.href;
    var start = location.origin.length + 1;
    var projectName = href.substring(start, href.indexOf("/", start));
    return $.ajax({
      url: Const.serverHost() + '/' + projectName + '/' + serviceName,
      type: 'POST',
      data,
      processData: false,
      contentType: false,
      dataType: 'json'
    })
      .then((...args) => {
        const [data, textStatus, jqXHR] = args;

        // 
        if (this.fnCheckCommonError(data) == false) {
          return false;
        }
        if (fncSuccess != null) {
          fncSuccess(data);
        }
        return true;
      })
      .catch((...args) => {
        const [jqXHR, textStatus, errorThrown] = args;

        if (fncError != null) {
          fncError();
        } else {
          alert(textStatus);
          this.dispError(textStatus, errorThrown['message']);
        }
        return false;
      })
      .then((value) => {
        // waiting
        this.fnWaitScreen(WebService.SCREEN_MODE_MINUS);
        return value;
      });
  }


  /**
   * 
   */
  fnCheckCommonError(response: any): boolean {

    if (response.fatalError.length > 0) {
      const errors = response.fatalError;
      for (let i = 0; i < errors.length; i++) {
        if (errors[i].errId == 'MC000001'
          || errors[i].errId == 'MC000002'
          || errors[i].errId == 'MC000003') {
          // TODO:
          // dispError(errors[i].errId,errors[i].errMsg);

          // TODO:
          // this.errorInfoSpcm00001.errId = errors[i].errId;
          // this.errorInfoSpcm00001.errMsg = errors[i].errMsg;

          if (isDevMode()) {
            //  if (!this.windowAlert.confirm('API')) { return false; }
          }
          var data: IObjectString = {};
          data["HEADERDISPLAY"] = false;
          this.appService.toggle(data);
          this.router.navigate(["/Login"]);
          return false;
        }
      }
    }

    return true;
  }


  /**
   * 
   * @param code
   * @param msg
   */
  dispError(code: string, msg: string): void {
    let errorStr = '';
    errorStr += '\n\n';
    errorStr += ':' + code + '\n';
    errorStr += ':' + msg + '\n';

    window.alert(errorStr)
    return;
  }

}
