import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { FTOT0010StoreService } from 'src/app/services/FTOT0010/ftot0010-store.service';
import { TableComponent } from '../../base/table/table/table.component';
import { IObjectString } from 'src/app/common/IObject';

@Component({
  selector: 'app-stot00101',
  templateUrl: './stot00101.component.html'
})
export class STOT00101Component extends BaseComponent implements OnInit {
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00101;


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected FTOT00101Store: FTOT0010StoreService,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  numF: any = 0;
  numK: any = 0;
  numH: any = 0;
  numG: any = 0;
  numS: any = 0;
  lstData: any = [];
  incodeMessage: string = "";
  @ViewChild("incode") incode: InputComponent | any;

  fnInit() {
    if (this.appService.IDXMENU == 0) {
      this.FTOT00101Store.SCREENPCK = "1";
    } else {
      this.FTOT00101Store.SCREENPCK = "2";
    }
    this.fnGetData();
    this.incode.focusIn();
  }

  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'incode':
        this.fnCheckSoordno();
        break;
    }
  }

  fnCheckSoordno() {
    var incode = this.incode.getVal();
    if (Ultility.isEmpty(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      var msg = this.message.message["ME000004"].replace("%1", this.formItemNm[9]);
      this.notifyError(this.appService, msg, () => {
        this.incode.focusIn();
      })
      return;
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "SOORDNO": incode,
        "SCREENPCK": this.FTOT00101Store.SCREENPCK
      }
    }
    return this.webService.callWs(Const.stot00101CheckSoordNoWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.incode.setVal("");
          this.incode.focusIn();
        });
        return;
      } else {
        Ultility.handyCtrl(this.ledService, Const.LedGreen, true, Const.SoundSuccess);
        if (incode == response.SOORDNO) {
          this.FTOT00101Store.SOORDNO = response.SOORDNO;
          this.FTOT00101Store.FLGCHECKSOORDNO = "1";
          if (this.FTOT00101Store.SCREENPCK == "1") {
            this.router.navigate([DisplayScreenID.STOT00103]);
          } else {
            this.router.navigate([DisplayScreenID.STOT00107]);
          }
        }
      }
    })
  }

  btnBack() {
    this.FTOT00101Store.SOORDNO = "";
    this.backToMenu();
  }

  // init load data
  fnGetData() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "SCREENPCK": this.FTOT00101Store.SCREENPCK
      }
    }

    return this.webService.callWs(Const.stot00101SearchDataWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.incode.focusIn();
        return;
      } else {
        this.lstData = response.stot0010DataDto;
        this.numS = response.SUM;
        this.FTOT00101Store.FLGCHECKSOORDNO = "0";
        for (let i = 0; i < this.lstData.length; i++) {
          if (this.lstData[i].CSTMITEMGRPCD == "BEAUTY") {
            this.numG = this.lstData[i].QTY
          } else if (this.lstData[i].CSTMITEMGRPCD == "FASHION") {
            this.numF = this.lstData[i].QTY
          } else if (this.lstData[i].CSTMITEMGRPCD == "HOME") {
            this.numH = this.lstData[i].QTY
          } else if (this.lstData[i].CSTMITEMGRPCD == "KIDS") {
            this.numK = this.lstData[i].QTY
          }
        }
        this.incode.focusIn();
      }
    })

  }

  //click item
  btnClick(key: any) {
    if (key == "K") {
      this.FTOT00101Store.TYPEKEY = "K";
    } else if (key == "H") {
      this.FTOT00101Store.TYPEKEY = "H";
    } else if (key == "F") {
      this.FTOT00101Store.TYPEKEY = "F";
    } else if (key == "G") {
      this.FTOT00101Store.TYPEKEY = "G";
    }
    this.FTOT00101Store.FLGPCK = "0";
    this.router.navigate([DisplayScreenID.STOT00102]);
  }


  //click 
  btnSum(key: any) {
    this.FTOT00101Store.TYPEKEY = "";
    this.FTOT00101Store.FLGPCK = "1";
    this.router.navigate([DisplayScreenID.STOT00102]);
  }
}
