import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { TableComponent } from '../../base/table/table/table.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0040StoreService } from 'src/app/services/FTOT0040/ftot0040-store.service';

@Component({
  selector: 'app-stot00403',
  templateUrl: './stot00403.component.html'
})
export class STOT00403Component extends BaseComponent implements OnInit {

  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0040STORE: FTOT0040StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  incodeMessage: string = "";
  @ViewChild("incode") incode: InputComponent | any;
  ITEMNM: string = "";
  SHIPMNTORDQTY: string = "";
  SHIPMNTRSLTQTY: string = "";
  GIFTDETAIL: string = "";
  LIQUIDDETAIL: string = "";
  BROKENDETAIL: string = "";
  WRAPPINGFLG: string = "";
  HEMMING: string = "";
  ADDITEMFLG: string = "";
  SIZE: string = "";
  COLOR: string = "";
  listShipment: any = [];
  optionlisttemp: any = [];
  optionlist: string = "";
  linkImg: string = "";

  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00403;

  fnInit() {
    this.listShipment = this.FTOT0040STORE.listShipment;
    this.ITEMNM = this.listShipment[this.FTOT0040STORE.NowIndex].ITEMNM;
    this.SHIPMNTORDQTY = this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTORDQTY;
    this.SHIPMNTRSLTQTY = this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY;
    this.GIFTDETAIL = this.listShipment[this.FTOT0040STORE.NowIndex].GIFTDETAIL == 1 ? this.formItemNm[9] : '';
    this.LIQUIDDETAIL = this.listShipment[this.FTOT0040STORE.NowIndex].LIQUIDDETAIL == 1 ? this.formItemNm[10] : '';
    this.BROKENDETAIL = this.listShipment[this.FTOT0040STORE.NowIndex].BROKENDETAIL == 1 ? this.formItemNm[11] : '';
    this.WRAPPINGFLG = this.listShipment[this.FTOT0040STORE.NowIndex].WRAPPINGFLG == 1 ? this.formItemNm[12] : '';
    this.HEMMING = this.listShipment[this.FTOT0040STORE.NowIndex].HEMMING == 1 ? this.formItemNm[13] : '';
    this.ADDITEMFLG = this.listShipment[this.FTOT0040STORE.NowIndex].ADDITEMFLG == 1 ? this.formItemNm[14] : '';
    this.SIZE = this.listShipment[this.FTOT0040STORE.NowIndex].SIZE;
    this.COLOR = this.listShipment[this.FTOT0040STORE.NowIndex].COLOR;
    this.optionlist = this.listShipment[this.FTOT0040STORE.NowIndex].OPTIONVAL;
    this.linkImg = "http://webcoban.vn/image/flower.gif";
    

    if (!Ultility.isEmpty(this.optionlist)) {
      this.fnCheckOption();
    } else {
      this.optionlisttemp = [];
    }

    this.incode.focusIn();

  }

  fnDestroy() {
  }

  fnCheckOption() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00403),
      "stot0040DetailOtpList": this.optionlisttemp,
      "stot0040Condition": {
        "OPTIONVAL": this.optionlist
      }
    }
    return this.webService.callWs(Const.stot00403CheckOptionWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.incodeMessage = response.fatalError[0].errMsg;
        this.incode.focusIn();
        this.incode.selectAll();
        return;
      } else {
        this.optionlisttemp = response.stot0040Data;
        this.FTOT0040STORE.OPTIONLST = this.optionlisttemp;
        this.incode.focusIn();
      }
    })
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'incode':
        this.fnScan(this.incode.getVal());
        break;
    }
  }

  fnScan(incode: any) {
    this.incodeMessage = "";
    if (Ultility.isEmpty(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[8]);
      this.incode.focusIn();
      return;
    } else if (incode.length > 25) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000050"].replace("%1", 25);
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00403),
      "stot0040Condition": {
        "INCODE": incode
      }
    }
    return this.webService.callWs(Const.stot00403CheckCodeWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.incode.focusIn();
        this.incode.selectAll();
      } else {
        if (response.CHECKNUMFLG) {
          if (parseInt(incode) <= parseInt(this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTORDQTY)) {
            this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTYBEFORE = this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY;
            this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY = incode;
            this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTAVAILABLEQTY = this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTORDQTY - this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY;
            this.incode.setVal("");
            this.fnInit();
            return;
          } else {
            Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
            this.incodeMessage = this.message.message["ME000025"].replace("%1", this.formItemNm[8]);
            this.incode.focusIn();
            this.incode.selectAll();
            return;
          }
        } else {
          let existFlg: boolean = false;
          if (this.listShipment[this.FTOT0040STORE.NowIndex].ITEMCD == response.stot0040Condition.ITEMCD) {
            existFlg = true;
          } else {
            existFlg = false;
            this.FTOT0040STORE.LotChckFlg = 0;
            for (let i = 0; i < this.listShipment.length; i++) {
              if (this.listShipment[i].ITEMCD == response.stot0040Condition.ITEMCD) {
                this.FTOT0040STORE.NowIndex = i;
                existFlg = true;
                break;
              }
            }
          }
          if (existFlg == false) {
            Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
            this.incodeMessage = this.message.message["ME000024"].replace("%1", this.formItemNm[8]);
            this.incode.focusIn();
            this.incode.selectAll();
            return;
          }
          if (this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTAVAILABLEQTY <= 0) {
            Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
            this.incodeMessage = this.message.message["ME000025"].replace("%1", this.formItemNm[8]);
            this.incode.focusIn();
            this.incode.selectAll();
            return;
          } else {
            this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTYBEFORE = this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY;
            this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY = parseInt(this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY) + 1;
            this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTAVAILABLEQTY = parseInt(this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTORDQTY) - parseInt(this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY);
          }
          this.FTOT0040STORE.listShipment = this.listShipment;
          if (this.listShipment[this.FTOT0040STORE.NowIndex].LOTFLG == "0") {
            this.incode.setVal("");
            this.incode.focusIn();
            this.fnInit();
          } else if (this.listShipment[this.FTOT0040STORE.NowIndex].LOTFLG == "1" && this.FTOT0040STORE.LotChckFlg == 0) {
            this.router.navigate([DisplayScreenID.STOT00404]);
          } else {
            this.incode.setVal("");
            this.incode.focusIn();
            this.fnInit();
          }
        }
      }
    })
  }
  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00402]);
  }

  btnNext() {
    var cmpflg: boolean = true;
    for (let i = 0; i < this.listShipment.length; i++) {
      if (this.listShipment[i].SHIPMNTORDQTY != this.listShipment[i].SHIPMNTRSLTQTY) {
        cmpflg = false;
      }
    }
    if (cmpflg) {
      this.router.navigate([DisplayScreenID.STOT00405]);
    } else {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000175"];
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    }
  }

}
