import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { FTOT0010StoreService } from 'src/app/services/FTOT0010/ftot0010-store.service';
import { IObjectString } from 'src/app/common/IObject';
import { TableComponent } from '../../base/table/table/table.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-stot00103',
  templateUrl: './stot00103.component.html'
})
export class STOT00103Component extends BaseComponent implements OnInit {

  fnDestroy() {
  }

  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected FTOT00101Store: FTOT0010StoreService,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  @ViewChild("incode") incode: InputComponent | any;
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00103;
  jancd: string = "";
  itemnm: string = "";
  incodeMessage: string = "";
  CSTMRSVNO: string = "";
  LCTNEXISTMSG: string = "";
  pckordqty: any = 0;
  pckrsltqty: any = 0;
  lstEC: any = [];
  pckrslFlg: boolean = false;
  fnInit() {
    this.FTOT00101Store.FLGSCAN = "";
    if (this.FTOT00101Store.SCREENPCK == "1") {
      this.pckrslFlg = false;
    } else {
      this.pckrslFlg = true;
    }
    this.fnGetData();

  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'incode':
        var incode = this.incode.getVal();
        this.fnScan(incode, 1);
        break;
    }
  }


  //check incode
  fnScan(incode: any, flagScan: any) {
    this.incodeMessage = "";
    if (Ultility.isEmpty(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[3]);
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    } else if (incode.length > 25) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000050"].replace("%1", 25);
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "INCODE": incode,
      }
    }
    return this.webService.callWs(Const.stot00103CheckItemcdWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.incodeMessage = response.fatalError[0].errMsg;
        this.incode.focusIn();
        this.incode.selectAll();
        return;
      } else {
        let existsItemcd: boolean = false;
        let a = 0;
        let b = 0;
        for (let i = 0; i < this.lstEC.length; i++) {
          if (response.stot0010ConditionDto.ITEMCD == this.lstEC[i].ITEMCD) {
            a = a + 1;
          }
        }
        for (let i = 0; i < this.lstEC.length; i++) {
          if (response.stot0010ConditionDto.ITEMCD == this.lstEC[i].ITEMCD) {
            var result;
            if (this.FTOT00101Store.SCREENPCK == "1") {
              result = parseInt(this.lstEC[i].PCKORDQTY) - parseInt(this.lstEC[i].PCKRSLTQTY_B);
            } else {
              result = parseInt(this.lstEC[i].PCKORDQTY) - parseInt(this.lstEC[i].PCKRSLTQTY);
            }
            if (result > 0) {
              this.FTOT00101Store.lstScanItemcd = this.lstEC;
              this.FTOT00101Store.INDEX = i;
              this.FTOT00101Store.ITEMCD = this.lstEC[i].ITEMCD;
              this.FTOT00101Store.LCTNCD1 = this.lstEC[i].LCTNCD1;
              existsItemcd = true;
            } else if (result < 0 || result == 0) {
              b = b + 1;
            }
            if (b == a) {
              if (result < 0) {
                this.incodeMessage = this.message.message["ME000998"];
                this.incode.focusIn();
                this.incode.selectAll();
                return;
              } else {
                this.notifyError(this.appService, this.message.message["ME000029"].replace("%1", this.formItemNm[2]), () => {
                  this.incode.focusIn();
                  this.incode.selectAll();
                })
                return;
              }
            }
          }
        }

        if (existsItemcd == false) {
          this.fnCheckQty(response.stot0010ConditionDto.ITEMCD);
        } else {
          this.FTOT00101Store.LotDispFlg = response.LotDispFlg;
          this.FTOT00101Store.BEFORESCREEN = "STOT00103";
          this.FTOT00101Store.FLGSCAN = flagScan;
          if (response.LotDispFlg == "1") {
            this.router.navigate([DisplayScreenID.STOT00106]);
          } else {
            this.router.navigate([DisplayScreenID.STOT00105]);
          }
        }
      }
    })
  }

  fnGetData() {
    var CSTMITEMGRPCD = ""
    if (this.FTOT00101Store.FLGPCK == "0") {
      if (this.FTOT00101Store.TYPEKEY == "K") {
        CSTMITEMGRPCD = "KIDS";
      } else if (this.FTOT00101Store.TYPEKEY == "H") {
        CSTMITEMGRPCD = "HOME";
      } else if (this.FTOT00101Store.TYPEKEY == "F") {
        CSTMITEMGRPCD = "FASHION";
      } else if (this.FTOT00101Store.TYPEKEY == "G") {
        CSTMITEMGRPCD = "BEAUTY";
      }
    }
    var soordno = this.FTOT00101Store.SOORDNO;
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "SCREENPCK": this.FTOT00101Store.SCREENPCK,
        "FLGPCK": this.FTOT00101Store.FLGPCK,
        "TRFLG": this.FTOT00101Store.TRFLG,
        "CSTMITEMGRPCD": CSTMITEMGRPCD,
        "SOORDNO": soordno
      }
    }

    return this.webService.callWs(Const.stot00103GetDataWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.incode.focusIn();
        })
        return;
      } else {
        this.lstEC = response.stot0010DataDto;
        if (this.FTOT00101Store.FLGCHECKSOORDNO == "1") {
          this.CSTMRSVNO = response.stot0010DataDto[0].CSTMRSVNO;
          this.LCTNEXISTMSG = response.stot0010DataDto[0].LCTNEXISTMSG;
        } else {
          this.CSTMRSVNO = this.FTOT00101Store.CSTMRSVNO;
          this.LCTNEXISTMSG = this.FTOT00101Store.LCTNEXISTMSG;
        }
        this.FTOT00101Store.lstScanItemcd = this.lstEC;
        this.incode.focusIn();
      }
    })
  }

  btnBack() {
    if (this.FTOT00101Store.SCREENPCK == '1') {
      this.FTOT00101Store.CSTMRSVNO = "";
      this.FTOT00101Store.SOORDNO = "";
      this.FTOT00101Store.TRFLG = "";
      if (this.FTOT00101Store.FLGCHECKSOORDNO == "1") {
        this.router.navigate([DisplayScreenID.STOT00101]);
      } else {
        this.router.navigate([DisplayScreenID.STOT00102]);
      }
    } else {
      this.router.navigate([DisplayScreenID.STOT00107]);
    }
  }

  btnClickDetail(numEC: any, itemcd: any, PCKORDQTY: any, PCKRSLTQTY: any, PCKRSLTQTY_B: any) {
    if (this.FTOT00101Store.SCREENPCK == "1") {
      if (PCKORDQTY > PCKRSLTQTY_B) {
        this.fnScan(itemcd, 0);
      } else {
        this.notifyError(this.appService, this.message.message["ME000029"].replace("%1", this.formItemNm[2]), () => {
          this.incode.focusIn();
        })
        return;
      }
    } else {
      if (PCKORDQTY > PCKRSLTQTY) {
        this.fnScan(itemcd, 0);
      } else {
        this.notifyError(this.appService, this.message.message["ME000029"].replace("%1", this.formItemNm[2]), () => {
          this.incode.focusIn();
        })
        return;
      }
    }
  }

  fnCheckQty(itemcd: any) {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "TRFLG": this.FTOT00101Store.TRFLG,
        "ITEMCD": itemcd,
        "SOORDNO": this.FTOT00101Store.SOORDNO
      }
    }
    return this.webService.callWs(Const.stot00103CheckWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.incode.focusIn();
        this.incode.selectAll();
        return;
      } else {
        if (response.count > 0) {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
          this.incodeMessage = this.message.message["ME000995"];
          this.incode.focusIn();
          this.incode.selectAll();
          return;
        } else {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
          this.incodeMessage = this.message.message["ME000996"];
          this.incode.focusIn();
          this.incode.selectAll();
          return;
        }
      }
    })
  }
}
