import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import * as Const from '../../../common/Const';
import { IObjectString } from 'src/app/common/IObject';
import { Ultility } from 'src/app/services/common/ultility.service';
import { LedService } from 'src/app/services/common/led.service';
import { AppService } from 'src/app/services/common/app.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { NgbAccordion } from '@ng-bootstrap/ng-bootstrap';
import { FTCM0020StoreService } from 'src/app/services/FTCM0020/ftcm0020-store.service';

@Component({
  selector: 'app-stcm00201',
  templateUrl: './stcm00201.component.html'
})

export class STCM00201Component extends BaseComponent implements OnInit {
  @ViewChild("ngbAccordion") ngbAccordion: NgbAccordion | any;

  constructor(protected loginInfo: LoginInfoService,
    private FTCM0020Store: FTCM0020StoreService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    protected appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STCM00201;

  cstmcd = "";
  lstCstmcd = [];

  mainCstmcd = "";
  brnchcd = "";
  lstBrnchcd = [];
  mainBrnchcd = "";
  lang = "";
  lstLanguage = [];
  mainLang = "";

  async fnInit() {
    // Init list cstmcd and brnchcd
    this.cstmcd = this.FTCM0020Store.cstmcd;
    this.brnchcd = this.FTCM0020Store.brnchcd;
    this.lstBrnchcd = this.FTCM0020Store.lstBrnchcd;
    this.lstCstmcd = this.FTCM0020Store.lstCstmcd;
    this.fnBrnchcdChange();
    this.fnCstmcdChange();
  }

  fnBrnchcdChange() {
    if (this.brnchcd == this.menu.storeBrnchcd) {
      return;
    }

    for (let row of this.lstBrnchcd) {
      //BRNCHCD
      if (row["BRNCHCD"] == this.brnchcd) {
        this.loginInfo.brnchcd = row["BRNCHCD"];
        this.loginInfo.brnchnm = row["BRNCHNM"];
        this.menu.storeBrnchcd = row["BRNCHCD"];
        break;
      }
    }
    // this.FTCM0020Store.brnchcd = this.brnchcd
    var data: IObjectString = {};
    data["LOGOUT"] = this.formItemNm[4];
    data["USRNM"] = this.loginInfo.usernm;
    data["CSTMNM"] = this.loginInfo.cstmnm;
    data["BRNCHNM"] = this.loginInfo.brnchnm;
    data["SCREENTITLE"] = this.formItemNm[0];
    data["HEADERDISPLAY"] = true;
    this.appService.toggle(data);

    this.menu.isInitMenu = true;
  }

  fnCstmcdChange() {
    if (this.cstmcd == this.menu.storeCstmcd) {
      return;
    }

    for (let row of this.lstCstmcd) {
      //BRNCHCD
      if (row["CSTMCD"] == this.cstmcd) {
        this.loginInfo.cstmcd = row["CSTMCD"];
        this.loginInfo.cstmnm = row["CSTMNM"];
        this.menu.storeCstmcd = row["CSTMCD"];
        break;
      }
    }
    // this.FTCM0020Store.cstmcd = this.cstmcd


    var data: IObjectString = {};
    data["LOGOUT"] = this.formItemNm[4];
    data["USRNM"] = this.loginInfo.usernm;
    data["CSTMNM"] = this.loginInfo.cstmnm;
    data["BRNCHNM"] = this.loginInfo.brnchnm;
    data["SCREENTITLE"] = this.formItemNm[0];
    data["HEADERDISPLAY"] = true;
    this.appService.toggle(data);

    this.menu.isInitMenu = true;
  }

  fnDestroy() {

  }

  btnBack() {
    this.router.navigate([DisplayScreenID.STCM00101])
  }

  btnNext() {
    this.FTCM0020Store.BeforeScreenLg = 'STCM00201';
    this.router.navigate([DisplayScreenID.STCM00202])
  }
}
