import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { FTOT0010StoreService } from 'src/app/services/FTOT0010/ftot0010-store.service';
import { IObjectString } from 'src/app/common/IObject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-stot00106',
  templateUrl: './stot00106.component.html'
})
export class STOT00106Component extends BaseComponent implements OnInit {


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected FTOT00101Store: FTOT0010StoreService,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  @ViewChild("limitdate") limitdate: InputComponent | any;
  @ViewChild("stckmngkey1") stckmngkey1: InputComponent | any;
  @ViewChild("stckmngkey2") stckmngkey2: InputComponent | any;
  @ViewChild("stckmngkey3") stckmngkey3: InputComponent | any;

  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00106;
  incodeMessage: string = "";
  jancd: string = "";
  itemnm: string = "";
  itemrnm: string = "";
  qtynm: string = "";
  lctncd: string = "";
  pckordqty: string = "";
  pckrsltqty: string = "";
  limitdateMessage: string = "";
  stckmngkey1Message: string = "";
  stckmngkey2Message: string = "";
  stckmngkey3Message: string = "";

  fnInit() {
    this.fnGetData();
  }

  fnDestroy() {
  }

  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00103])
  }

  btnNext() {

    // Check form input valid
    if (this.fnCheckFormValid() == true) {
      this.limitdate.focusIn();
      return;
    }

    let limitdate = this.limitdate.getVal();
    let stckmngkey1 = Ultility.isEmpty(this.stckmngkey1.getVal()) ? '' : this.stckmngkey1.getVal();
    let stckmngkey2 = Ultility.isEmpty(this.stckmngkey2.getVal()) ? '' : this.stckmngkey2.getVal();
    let stckmngkey3 = Ultility.isEmpty(this.stckmngkey3.getVal()) ? '' : this.stckmngkey3.getVal();


    if (limitdate != this.FTOT00101Store.LIMITDATE || stckmngkey1 != this.FTOT00101Store.STCKMNGKEY1 ||stckmngkey2 != this.FTOT00101Store.STCKMNGKEY2 || stckmngkey3 != this.FTOT00101Store.STCKMNGKEY3) {
      this.notifyError(this.appService, this.message.message["ME000115"], () => {
        this.limitdate.focusIn();
        this.limitdate.selectAll();
      })
      return;
    }
    this.FTOT00101Store.BEFORESCREEN = "STOT00106"
    this.router.navigate([DisplayScreenID.STOT00105])
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  fnEntInput($event: any, currentFocus: String) {
    switch (currentFocus) {
      case 'limitdate':
        this.stckmngkey1.focusIn();
        this.stckmngkey1.selectAll();
        break;
      case 'stckmngkey1':
        this.stckmngkey2.focusIn();
        this.stckmngkey2.selectAll();
        break;
      case 'stckmngkey2':
        this.stckmngkey3.focusIn();
        this.stckmngkey3.selectAll();
        break;
      case 'stckmngkey3':
        this.limitdate.focusIn();
        this.limitdate.selectAll();
        break;
    }
  }

  fnGetData() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0010ConditionDto": {
        "SOORDNO": this.FTOT00101Store.SOORDNO,
        "SCREENPCK": this.FTOT00101Store.SCREENPCK,
        "TRFLG": this.FTOT00101Store.TRFLG,
        "ITEMCD": this.FTOT00101Store.ITEMCD,
        "LCTNCD": this.FTOT00101Store.LCTNCD1
      }
    }
    return this.webService.callWs(Const.stot00106GetDataWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.limitdate.focusIn();
        })
        return;
      } else {
        this.FTOT00101Store.LCTNCD = this.FTOT00101Store.LCTNCD1;
        this.FTOT00101Store.TRFLG = response.stot00106DataDto.TRFLG;
        this.FTOT00101Store.ITEMCD = response.stot00106DataDto.ITEMCD;
        this.FTOT00101Store.SODTLNO = response.stot00106DataDto.SODTLNO;
        this.FTOT00101Store.JANCD = response.stot00106DataDto.JANCD;
        this.FTOT00101Store.ITEMNM = response.stot00106DataDto.ITEMNM;
        this.FTOT00101Store.ITEMRNM = response.stot00106DataDto.ITEMRNM;
        this.FTOT00101Store.QLTYCD = response.stot00106DataDto.QLTYCD;
        this.FTOT00101Store.QLTYNM = response.stot00106DataDto.QLTYNM;
        this.FTOT00101Store.PCKNGLSTNO = Ultility.isEmpty(response.stot00106DataDto.PCKNGLSTNO) ? '' : response.stot00106DataDto.PCKNGLSTNO;
        this.FTOT00101Store.LIMITDATE = Ultility.isEmpty(response.stot00106DataDto.LIMITDATE) ? '' : response.stot00106DataDto.LIMITDATE;
        this.FTOT00101Store.STCKMNGKEY1 = Ultility.isEmpty(response.stot00106DataDto.STCKMNGKEY1) ? '' : response.stot00106DataDto.STCKMNGKEY1;
        this.FTOT00101Store.STCKMNGKEY2 = Ultility.isEmpty(response.stot00106DataDto.STCKMNGKEY2) ? '' : response.stot00106DataDto.STCKMNGKEY2;
        this.FTOT00101Store.STCKMNGKEY3 = Ultility.isEmpty(response.stot00106DataDto.STCKMNGKEY3) ? '' : response.stot00106DataDto.STCKMNGKEY3;
        this.FTOT00101Store.SIZE = response.stot00106DataDto.SIZE;
        this.FTOT00101Store.COLOR = response.stot00106DataDto.COLOR;
        this.FTOT00101Store.OPTIONLST = response.stot00106DataDto.OPTIONLST;
        this.FTOT00101Store.ECFLG = response.stot00106DataDto.ECFLG;
        this.FTOT00101Store.URL = response.stot00106DataDto.URL;
        this.FTOT00101Store.PCKORDQTY = response.stot00106DataDto.PCKORDQTY;
        this.FTOT00101Store.PCKRSLTQTY = response.stot00106DataDto.PCKRSLTQTY;
        this.FTOT00101Store.PCKRSLTQTY_B = response.stot00106DataDto.PCKRSLTQTY_B;
        this.FTOT00101Store.WRAPPINGFLG = response.stot00106DataDto.WRAPPINGFLG;
        this.FTOT00101Store.HEMMINGFLG = response.stot00106DataDto.HEMMINGFLG;
        this.jancd = response.stot00106DataDto.JANCD;
        this.itemnm = response.stot00106DataDto.ITEMNM;
        this.qtynm = response.stot00106DataDto.QLTYNM;
        this.limitdate.setVal("");
        this.stckmngkey1.setVal("");
        this.stckmngkey2.setVal("");
        this.stckmngkey3.setVal("");
        this.limitdate.focusIn();
      }
    })
  }

  fnCheckFormValid() {
    var hasError = false;
    if (!Ultility.isEmpty(this.limitdate.getVal())) {
      if (!Ultility.isDate(this.limitdate.getVal())) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.limitdateMessage = this.message.message["ME000006"].replace("%1", this.formItemNm[4]);
        hasError = true;
      }
    }

    if (!Ultility.isEmpty(this.stckmngkey1.getVal())) {
      if (this.stckmngkey1.getVal().length > 20) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.stckmngkey1Message = this.message.message["ME000050"].replace("%1", 20);
        hasError = true;
      }
      if (this.stckmngkey1.getVal().length ==0) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.stckmngkey1Message = this.message.message["ME000004"].replace("%1", "1");
        hasError = true;
      }
    }

    if (!Ultility.isEmpty(this.stckmngkey2.getVal())) {
      if (this.stckmngkey2.getVal().length > 20) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.stckmngkey2Message = this.message.message["ME000050"].replace("%1", 20);
        hasError = true;
      }
    }

    if (!Ultility.isEmpty(this.stckmngkey3.getVal())) {
      if (this.stckmngkey3.getVal().length > 20) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.stckmngkey3Message = this.message.message["ME000050"].replace("%1", 20);
        hasError = true;
      }
    }
    return hasError;
  }

  fnClearMessage() {
    this.limitdateMessage = "";
    this.stckmngkey1Message = "";
    this.stckmngkey2Message = "";
    this.stckmngkey3Message = "";
  }
}

