import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { FTIN0110StoreService } from 'src/app/services/FHIN0110/ftin0110-store.service';

@Component({
  selector: 'app-stin01101',
  templateUrl: './stin01101.component.html'
})
export class STIN01101Component extends BaseComponent implements OnInit {
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STIN01101;


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected FTIN01101Store: FTIN0110StoreService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  incodeMessage: string = "";

  @ViewChild("incode") incode: InputComponent | any;

  fnInit() {
    this.incode.focusIn();
  }

  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput() {
    this.fnCheckItemCd();
  }
  //Check ITEMCD
  fnCheckItemCd() {
    var incode = this.incode.getVal();
    if (Ultility.isEmpty(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      var msg = this.message.message["ME000004"].replace("%1", this.formItemNm[1]);
      this.notifyError(this.appService, msg, () => {
        this.incode.focusIn();
      })
      return;
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stin0110ConditionDtoRequest": {
        "ITEMCD": incode
      }
    }
    return this.webService.callWs(Const.stin01101CheckItemCdWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, response.fatalError[0].errMsg.replace("%1", this.formItemNm[1]), () => {
          this.incode.setVal("");
          this.incode.focusIn();
        });
        return;
      } else {
        Ultility.handyCtrl(this.ledService, Const.LedGreen, true, Const.SoundSuccess);
        this.FTIN01101Store.ITEMCD = response.stin0110ConditionDtoResponse.ITEMCD;
        this.FTIN01101Store.ITEMNM = response.stin0110ConditionDtoResponse.ITEMNM;
        this.FTIN01101Store.ITEMRNM = response.stin0110ConditionDtoResponse.ITEMRNM;
        this.FTIN01101Store.QLTYCD = response.stin0110ConditionDtoResponse.QLTYCD;
        this.FTIN01101Store.QLTYNM = response.stin0110ConditionDtoResponse.QLTYNM;
        this.FTIN01101Store.QLTYRNM = response.stin0110ConditionDtoResponse.QLTYRNM;
        this.FTIN01101Store.CSTMRSVNO = response.stin0110ConditionDtoResponse.CSTMRSVNO;
        this.FTIN01101Store.SHIPFLG = response.stin0110ConditionDtoResponse.SHIPFLG;
        this.FTIN01101Store.ECFLG = response.stin0110ConditionDtoResponse.ECFLG;
        this.FTIN01101Store.ECLCTNCD = response.stin0110ConditionDtoResponse.ECLCTNCD;
        this.FTIN01101Store.SOORDNO = response.stin0110ConditionDtoResponse.SOORDNO;
        this.FTIN01101Store.SIPLNNO = response.stin0110ConditionDtoResponse.SIPLNNO;
        this.FTIN01101Store.HOLDRMNQTY = response.stin0110ConditionDtoResponse.HOLDRMNQTY;
        this.FTIN01101Store.TRLCTN1CD = response.stin0110ConditionDtoResponse.TRLCTN1CD;
        this.FTIN01101Store.ECLCTN1CD = response.stin0110ConditionDtoResponse.ECLCTN1CD;
        this.FTIN01101Store.ARVLRSLTQTY = "1";
        this.FTIN01101Store.ARVLTYPEMSG = response.stin0110ConditionDtoResponse.ARVLTYPEMSG;
        if(this.FTIN01101Store.SHIPFLG == "1") {
          this.FTIN01101Store.ARVLPLNREMAINQTY = response.listDetail[0].ARVLPLNREMAINQTY;
          this.FTIN01101Store.ARVLPLNQTY = response.listDetail[0].ARVLPLNQTY;
          this.FTIN01101Store.ARVLEDQTY = response.listDetail[0].ARVLEDQTY;
        }else {
          this.FTIN01101Store.HOLDRMNQTY = "";
          this.FTIN01101Store.ARVLPLNREMAINQTY = 0;
          this.FTIN01101Store.ARVLPLNQTY = 0;
        }
        this.router.navigate([DisplayScreenID.STIN01102]);
      }
    })
  }

  btnBack() {
    this.backToMenu();
  }

  btnNext(){
    this.fnCheckItemCd();
  }
}
