import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { TableComponent } from '../../base/table/table/table.component';
import { IObjectString } from 'src/app/common/IObject';
import { FTIN0010StoreService } from 'src/app/services/FTIN0010/ftin0010-store.service';

@Component({
  selector: 'app-stin00101',
  templateUrl: './stin00101.component.html'
})
export class STIN00101Component extends BaseComponent implements OnInit {
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STIN00101;


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTIN0010Store : FTIN0010StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  incodeMessage: string = "";
  @ViewChild("incode") incode: InputComponent | any;

  fnInit() {
    this.incode.focusIn();
    if (this.FTIN0010Store.prevPage != "") {
      this.incode.setVal("");
    }
  }

  fnDestroy() {
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
        case 'incode' : 
          this.fncheckItemCd(this.incode.getVal());
          break;
    }
  }

  //
  fncheckItemCd(incode : any){
    if(Ultility.isEmpty(incode)){
      Ultility.handyCtrl(this.ledService ,Const.LedRed ,true ,Const.SoundWarning );
      this.incodeMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[1]);
      this.incode.focusIn();
      return;
    }
    var request = {
      "accessInfo" : this.loginInfo.getAccessInfo(DisplayScreenID.STIN00101),
      "stin0010Condition" : {
        "SCANCD" : incode,
        "CommonLotDispFlg": this.getSystemEntity("CommonLotDispFlg") 
      }
    }
    return this.webService.callWs(Const.stin00101CheckItemCdWs ,request , (response) =>{
      if(Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)){
        this.notifyError(this.appService ,response.fatalError[0].errMsg , ()=>{
          this.incode.setVal("");
          this.incode.focusIn();
        })
        return;
      }else{
        if(response.CommonLotDispFlg = true){
          if(response.stin0010Condition.LIMITDATEKBN == "2"){
            Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
            this.notifyWarning(this.appService, this.message.message["MW000016"], () =>{
            this.FTIN0010Store.ITEMCD = response.stin0010Condition.ITEMCD;
            this.FTIN0010Store.ITEMNM = response.stin0010Condition.ITEMNM;
            this.FTIN0010Store.ITEMRNM = response.ITEMRNM;
            this.FTIN0010Store.QLTYCD = response.stin0010Condition.QLTYCD;
            this.FTIN0010Store.QLTYNM = response.stin0010Condition.QLTYNM;
            this.FTIN0010Store.QLTYRNM = response.stin0010Condition.QLTYRNM;
            this.FTIN0010Store.ARVLTYPEMSG = Ultility.isEmpty(response.stin0010Condition.ARVLTYPEMSG) ? "" : response.stin0010Condition.ARVLTYPEMSG ;
            this.FTIN0010Store.CSTMRSVNO = Ultility.isEmpty(response.stin0010Condition.CSTMRSVNO) ? "" : response.stin0010Condition.CSTMRSVNO ;
            this.FTIN0010Store.ARVLPLNQTY = response.stin0010Condition.ARVLPLNQTY;
            this.FTIN0010Store.ARVLPLNREMAINQTY = response.stin0010Condition.ARVLPLNREMAINQTY;
            this.FTIN0010Store.ARVLEDQTY = response.stin0010Condition.ARVLEDQTY;
            this.FTIN0010Store.SIDTLNO = response.stin0010Condition.SIDTLNO;
            this.FTIN0010Store.LIMITDATEKBN = response.stin0010Condition.LIMITDATEKBN;
            this.FTIN0010Store.MYCLORFLG = response.stin0010Condition.MYCLORFLG;
            this.FTIN0010Store.SHIPFLG = response.stin0010Condition.SHIPFLG;
            this.FTIN0010Store.ECFLG = response.stin0010Condition.ECFLG;
            this.FTIN0010Store.HOLDLCTNCD = response.stin0010Condition.HOLDLCTNCD;
            this.FTIN0010Store.TRLCTN1CD = response.stin0010Condition.TRLCTN1CD;
            this.FTIN0010Store.SIPLNNO = response.stin0010Condition.SIPLNNO;
            this.FTIN0010Store.SOORDNO = response.stin0010Condition.SIPLNNO;
            this.FTIN0010Store.sipLst = response.rows;
              if (this.getSystemEntity("SYSKBN3") != "1") {
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.LIMITDATE = response.rows[0].LIMITDATE;
                } else {
                  this.FTIN0010Store.LIMITDATE = "";
                }
              } else {
                this.FTIN0010Store.LIMITDATE = "";
              }
              if (this.getSystemEntity("SYSKBN4") != "1") {
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.SIDTLNO = response.rows[0].SIDTLNO;
                }
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.STCKMNGKEY1 = response.rows[0].STCKMNGKEY1;
                } else {
                  this.FTIN0010Store.STCKMNGKEY1 = "";
                }
              } else {
                this.FTIN0010Store.STCKMNGKEY1 = "";
              }
              if (this.getSystemEntity("SYSKBN5") != "1") {
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.STCKMNGKEY2 = response.rows[0].STCKMNGKEY2;
                } else {
                  this.FTIN0010Store.STCKMNGKEY2 = "";
                }
              } else {
                this.FTIN0010Store.STCKMNGKEY2 = "";
              }
              if (this.getSystemEntity("SYSKBN6") != "1") {
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.STCKMNGKEY3 = response.rows[0].STCKMNGKEY3;
                } else {
                  this.FTIN0010Store.STCKMNGKEY3 = "";
                }
              } else {
                this.FTIN0010Store.STCKMNGKEY3 = "";
              }
              if(this.FTIN0010Store.MYCLORFLG == "1"){
                this.FTIN0010Store.prevPage = "STIN00101";
                this.router.navigate([DisplayScreenID.STIN00103]);
              }else{
                this.FTIN0010Store.prevPage = "STIN00101";
                this.router.navigate([DisplayScreenID.STIN00102]);
              }
            })
          }else{
            this.FTIN0010Store.ITEMCD = response.stin0010Condition.ITEMCD;
            this.FTIN0010Store.ITEMNM = response.stin0010Condition.ITEMNM;
            this.FTIN0010Store.ITEMRNM = response.ITEMRNM;
            this.FTIN0010Store.QLTYCD = response.stin0010Condition.QLTYCD;
            this.FTIN0010Store.QLTYNM = response.stin0010Condition.QLTYNM;
            this.FTIN0010Store.QLTYRNM = response.stin0010Condition.QLTYRNM;
            this.FTIN0010Store.ARVLTYPEMSG = Ultility.isEmpty(response.stin0010Condition.ARVLTYPEMSG) ? "" : response.stin0010Condition.ARVLTYPEMSG ;
            this.FTIN0010Store.CSTMRSVNO = Ultility.isEmpty(response.stin0010Condition.CSTMRSVNO) ? "" : response.stin0010Condition.CSTMRSVNO ;
            this.FTIN0010Store.ARVLPLNQTY = response.stin0010Condition.ARVLPLNQTY;
            this.FTIN0010Store.ARVLPLNREMAINQTY = response.stin0010Condition.ARVLPLNREMAINQTY;
            this.FTIN0010Store.ARVLEDQTY = response.stin0010Condition.ARVLEDQTY;
            this.FTIN0010Store.SIDTLNO = response.stin0010Condition.SIDTLNO;
            this.FTIN0010Store.LIMITDATEKBN = response.stin0010Condition.LIMITDATEKBN;
            this.FTIN0010Store.MYCLORFLG = response.stin0010Condition.MYCLORFLG;
            this.FTIN0010Store.SHIPFLG = response.stin0010Condition.SHIPFLG;
            this.FTIN0010Store.ECFLG = response.stin0010Condition.ECFLG;
            this.FTIN0010Store.HOLDLCTNCD = response.stin0010Condition.HOLDLCTNCD;
            this.FTIN0010Store.TRLCTN1CD = response.stin0010Condition.TRLCTN1CD;
            this.FTIN0010Store.SIPLNNO = response.stin0010Condition.SIPLNNO;
            this.FTIN0010Store.SOORDNO = response.stin0010Condition.SIPLNNO;
            this.FTIN0010Store.sipLst = response.rows;
              if (this.getSystemEntity("SYSKBN3") != "1") {
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.LIMITDATE = response.rows[0].LIMITDATE;
                } else {
                  this.FTIN0010Store.LIMITDATE = "";
                }
              } else {
                this.FTIN0010Store.LIMITDATE = "";
              }
              if (this.getSystemEntity("SYSKBN4") != "1") {
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.SIDTLNO = response.rows[0].SIDTLNO;
                }
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.STCKMNGKEY1 = response.rows[0].STCKMNGKEY1;
                } else {
                  this.FTIN0010Store.STCKMNGKEY1 = "";
                }
              } else {
                this.FTIN0010Store.STCKMNGKEY1 = "";
              }
              if (this.getSystemEntity("SYSKBN5") != "1") {
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.STCKMNGKEY2 = response.rows[0].STCKMNGKEY2;
                } else {
                  this.FTIN0010Store.STCKMNGKEY2 = "";
                }
              } else {
                this.FTIN0010Store.STCKMNGKEY2 = "";
              }
              if (this.getSystemEntity("SYSKBN6") != "1") {
                if (Ultility.isEmpty(response.stin0010Condition.SIPLNNO) == false && response.rows.length == "1") {
                  this.FTIN0010Store.STCKMNGKEY3 = response.rows[0].STCKMNGKEY3;
                } else {
                  this.FTIN0010Store.STCKMNGKEY3 = "";
                }
              } else {
                this.FTIN0010Store.STCKMNGKEY3 = "";
              }
              if(this.FTIN0010Store.MYCLORFLG == "1"){
                this.FTIN0010Store.prevPage = "STIN00101";
                this.router.navigate([DisplayScreenID.STIN00103]);
              }else{
                this.FTIN0010Store.prevPage = "STIN00101";
                this.router.navigate([DisplayScreenID.STIN00102]);
              }
          }
        }else{
          this.FTIN0010Store.ITEMCD = response.stin0010Condition.ITEMCD;
          this.FTIN0010Store.ITEMNM = response.stin0010Condition.ITEMNM;
          this.FTIN0010Store.ITEMRNM = response.ITEMRNM;
          this.FTIN0010Store.QLTYCD = response.stin0010Condition.QLTYCD;
          this.FTIN0010Store.QLTYNM = response.stin0010Condition.QLTYNM;
          this.FTIN0010Store.QLTYRNM = response.stin0010Condition.QLTYRNM;
          this.FTIN0010Store.ARVLTYPEMSG = Ultility.isEmpty(response.stin0010Condition.ARVLTYPEMSG) ? "" : response.stin0010Condition.ARVLTYPEMSG ;
          this.FTIN0010Store.CSTMRSVNO = Ultility.isEmpty(response.stin0010Condition.CSTMRSVNO) ? "" : response.stin0010Condition.CSTMRSVNO ;
          this.FTIN0010Store.ARVLPLNQTY = response.stin0010Condition.ARVLPLNQTY;
          this.FTIN0010Store.ARVLPLNREMAINQTY = response.stin0010Condition.ARVLPLNREMAINQTY;
          this.FTIN0010Store.ARVLEDQTY = response.stin0010Condition.ARVLEDQTY;
          this.FTIN0010Store.SIDTLNO = response.stin0010Condition.SIDTLNO;
          this.FTIN0010Store.LIMITDATEKBN = response.stin0010Condition.LIMITDATEKBN;
          this.FTIN0010Store.MYCLORFLG = response.stin0010Condition.MYCLORFLG;
          this.FTIN0010Store.SHIPFLG = response.stin0010Condition.SHIPFLG;
          this.FTIN0010Store.ECFLG = response.stin0010Condition.ECFLG;
          this.FTIN0010Store.HOLDLCTNCD = response.stin0010Condition.HOLDLCTNCD;
          this.FTIN0010Store.TRLCTN1CD = response.stin0010Condition.TRLCTN1CD;
          this.FTIN0010Store.SIPLNNO = response.stin0010Condition.SIPLNNO;
          this.FTIN0010Store.SOORDNO = response.stin0010Condition.SIPLNNO;
          this.FTIN0010Store.sipLst = response.rows;
          if(this.FTIN0010Store.MYCLORFLG == "1"){
            this.FTIN0010Store.prevPage = "STIN00101";
            this.router.navigate([DisplayScreenID.STIN00103]);
          }else{
            this.FTIN0010Store.prevPage = "STIN00101";
            this.router.navigate([DisplayScreenID.STIN00102]);
          }
        }
      }
    })
  }

  btnBack() {
    this.FTIN0010Store.clear();
    this.backToMenu();
  }
  
  btnCheckCode(){
    this.fncheckItemCd(this.incode.getVal());
  }
}
