import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { TableComponent } from '../../base/table/table/table.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0040StoreService } from 'src/app/services/FTOT0040/ftot0040-store.service';
import { FTOT0010StoreService } from 'src/app/services/FTOT0010/ftot0010-store.service';

@Component({
  selector: 'app-stot00402',
  templateUrl: './stot00402.component.html'
})
export class STOT00402Component extends BaseComponent implements OnInit {
  fnDestroy() {
  }
  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0040STORE: FTOT0040StoreService,
    protected FTOT0010Store: FTOT0010StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00402;
  @ViewChild("incode") incode: InputComponent | any;
  CSTMRSVNO: string = "";
  DELIMTHDNM: string = "";
  DESIGNATEDTIMENM: string = "";
  DESIGNATEDDATE: string = "";
  incodeMessage: string = "";
  listShipment: any = [];
  fnInit() {
    if (this.FTOT0010Store.SOORDNO != "") {
      this.FTOT0040STORE.SOORDNO = this.FTOT0010Store.SOORDNO;
    }
    this.fnGetData();
    this.incode.setVal("");
    this.incode.focusIn();
  }

  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'incode':
        this.fnScan(this.incode.getVal());
        break;
    }
  }

  fnGetData() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00402),
      "stot0040Condition": {
        "SOORDNO": this.FTOT0040STORE.SOORDNO
      }
    }
    return this.webService.callWs(Const.stot00402GetDataWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          this.incode.setVal("");
          this.incode.focusIn();
        })
        return;
      } else {
        this.listShipment = response.listShipment;
        this.CSTMRSVNO = response.listShipment[0].CSTMRSVNO;
        this.DELIMTHDNM = response.listShipment[0].DELIMTHDNM;
        this.DESIGNATEDDATE = response.listShipment[0].DESIGNATEDDATE;
        this.DESIGNATEDTIMENM = response.listShipment[0].DESIGNATEDTIMENM;
        this.incode.focusIn();
      }
    })
  }

  fnScan(incode: any) {
    this.incodeMessage = "";
    if (Ultility.isEmpty(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[4]);
      this.incode.focusIn();
      return;
    } else if (incode.length > 25) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000050"].replace("%1", this.formItemNm[4]);
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00402),
      "stot0040Condition": {
        "INCODE": incode
      }
    }
    return this.webService.callWs(Const.stot00402CheckCodeWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.incode.selectAll();
        this.incode.focusIn();
        return;
      } else {
        let existFlg: boolean = false;
        for (let i = 0; i < this.listShipment.length; i++) {
          if (response.stot0040Condition.ITEMCD == this.listShipment[i].ITEMCD) {
            this.FTOT0040STORE.NowIndex = i;
            existFlg = true;
            break;
          }
        }
        if (existFlg == false) {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.incodeMessage = this.message.message["ME000024"].replace("%1", this.formItemNm[4]);
          this.incode.focusIn();
          this.incode.selectAll();
          return;
        }
        if (this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTAVAILABLEQTY <= 0) {
          Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
          this.incodeMessage = this.message.message["ME000025"].replace("%1", this.formItemNm[4]);
          this.incode.focusIn();
          this.incode.selectAll();
          return;
        } else {
          this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTYBEFORE = this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY;
          this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY = parseInt(this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY + 1);
          this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTAVAILABLEQTY = parseInt(this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTORDQTY) - parseInt(this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTRSLTQTY);
        }
        this.FTOT0040STORE.listShipment = this.listShipment;
        if (this.listShipment[this.FTOT0040STORE.NowIndex].LOTFLG == "0") {
          this.router.navigate([DisplayScreenID.STOT00403]);
        } else if (this.listShipment[this.FTOT0040STORE.NowIndex].LOTFLG == "1") {
          this.router.navigate([DisplayScreenID.STOT00404]);
        }
      }
    })
  }

  btnClickDetail(ITEMCD: any) {
    this.FTOT0040STORE.NowIndex = 0;
    let existFlg: boolean = false;
    for (let i = 0; i < this.listShipment.length; i++) {
      if (ITEMCD == this.listShipment[i].ITEMCD) {
        this.FTOT0040STORE.NowIndex = i;
        existFlg = true;
        break;
      }
    }
    if (existFlg == false) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000024"].replace("%1", this.formItemNm[4]);
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    }
    if (this.listShipment[this.FTOT0040STORE.NowIndex].SHIPMNTAVAILABLEQTY <= 0) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000025"].replace("%1", this.formItemNm[4]);
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    }
    this.FTOT0040STORE.listShipment = this.listShipment;
    if (this.listShipment[this.FTOT0040STORE.NowIndex].LOTFLG == "0") {
      this.router.navigate([DisplayScreenID.STOT00403]);
    } else if (this.listShipment[this.FTOT0040STORE.NowIndex].LOTFLG == "1") {
      this.router.navigate([DisplayScreenID.STOT00404]);
    }
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  btnBack() {
    if (this.FTOT0010Store.SOORDNO != "") {
      this.router.navigate([DisplayScreenID.STCM00203]);
    } else {
      this.router.navigate([DisplayScreenID.STOT00401]);
    }
  }
}
