import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0020StoreService } from 'src/app/services/FTOT0020/ftot0020-store.service';

@Component({
  selector: 'app-stot00204',
  templateUrl: './stot00204.component.html'
})
export class STOT00204Component extends BaseComponent implements OnInit {


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0020Store: FTOT0020StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  incodeMessage: string = "";
  @ViewChild("incode") incode: InputComponent | any;
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00204;
  ITEMNM: string = "";
  SHIPMNTORDQTY: string = "";
  SHIPMNTRSLTQTY: string = "";
  GIFTDETAIL: string = "";
  LIQUIDDETAIL: string = "";
  BROKENDETAIL: string = "";
  WRAPPINGFLG: string = "";
  HEMMING: string = "";
  ADDITEMFLG: string = "";
  SIZE: string = "";
  COLOR: string = "";
  lstShipment: any = [];
  optionlsttemp: any = [];
  optionlst: string = "";
  linkImg: string = "";

  fnInit() {
    this.lstShipment = this.FTOT0020Store.lstShipment;
    this.ITEMNM = this.lstShipment[this.FTOT0020Store.NowIndex].ITEMNM;
    this.SHIPMNTORDQTY = this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTORDQTY;
    this.SHIPMNTRSLTQTY = this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY;
    this.GIFTDETAIL = this.lstShipment[this.FTOT0020Store.NowIndex].GIFTDETAIL == 1 ? this.formItemNm[9] : '';
    this.LIQUIDDETAIL = this.lstShipment[this.FTOT0020Store.NowIndex].LIQUIDDETAIL == 1 ? this.formItemNm[10] : '';
    this.BROKENDETAIL = this.lstShipment[this.FTOT0020Store.NowIndex].BROKENDETAIL == 1 ? this.formItemNm[11] : '';
    this.WRAPPINGFLG = this.lstShipment[this.FTOT0020Store.NowIndex].WRAPPINGFLG == 1 ? this.formItemNm[12] : '';
    this.HEMMING = this.lstShipment[this.FTOT0020Store.NowIndex].HEMMING == 1 ? this.formItemNm[13] : '';
    this.ADDITEMFLG = this.lstShipment[this.FTOT0020Store.NowIndex].ADDITEMFLG == 1 ? this.formItemNm[14] : '';
    this.optionlst = this.lstShipment[this.FTOT0020Store.NowIndex].OPTION;
    this.linkImg = "http://webcoban.vn/image/flower.gif";

    if (!Ultility.isEmpty(this.optionlst)) {
      this.fnCheckOption();
    } else {
      this.optionlsttemp = [];
    }
    this.incode.focusIn();
  }

  fnCheckOption() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0020DetailOtpList": this.optionlsttemp,
      "stot0020ConditionDto": {
        "OPTION": this.optionlst,
      }
    }
    return this.webService.callWs(Const.stot00204CheckOptionWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.incodeMessage = response.fatalError[0].errMsg;
        this.incode.focusIn();
        this.incode.selectAll();
        return;
      } else {
        this.optionlsttemp = response.stot0020DataDto;
        this.FTOT0020Store.OPTIONLST = this.optionlsttemp;
        this.incode.focusIn();
      }
    })
  }

  fnDestroy() {
  }

  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00203]);
  }

  btnNext() {
    var cmpflg: boolean = true;
    for (let i = 0; i < this.lstShipment.length; i++) {
      if (this.lstShipment[i].SHIPMNTORDQTY != this.lstShipment[i].SHIPMNTRSLTQTY) {
        cmpflg = false;
      }
    }
    if (!cmpflg) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000175"];
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    } else {
      this.fnCheckScmFlg();
    }
  }

  fnCheckScmFlg() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
    }
    return this.webService.callWs(Const.stot00204CheckScmFlgWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.incodeMessage = response.fatalError[0].errMsg;
        this.incode.focusIn();
        this.incode.selectAll();
        return;
      } else {
        this.FTOT0020Store.ScmFlg = response.stot0020ConditionDto.ScmFlg;
        if (response.stot0020ConditionDto.ScmFlg == "0" && (this.lstShipment[0].DELIMDHCD == "1101" || this.lstShipment[0].DELIMDHCD == "1103")) {
          this.router.navigate([DisplayScreenID.STOT00207]);
        } else {
          this.router.navigate([DisplayScreenID.STOT00206]);
        }
      }
    })
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  fnScan() {
    this.incodeMessage = "";
    var incode = this.incode.getVal();
    if (Ultility.isEmpty(incode)) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000004"].replace("%1", this.formItemNm[3]);
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    } else if (incode.length > 25) {
      Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
      this.incodeMessage = this.message.message["ME000050"].replace("%1", 25);
      this.incode.focusIn();
      this.incode.selectAll();
      return;
    }
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0020ConditionDto": {
        "INCODE": incode,
      }
    }

    return this.webService.callWs(Const.stot00204ScanItemcdWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.incodeMessage = response.fatalError[0].errMsg;
        this.incode.focusIn();
        this.incode.selectAll();
        return;
      } else {
        if (response.CHECKNUMFLG) {
          if (parseInt(incode) <= parseInt(this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTORDQTY)) {
            this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTYBEFORE = this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY;
            this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY = incode;
            this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTAVAILABLEQTY = parseInt(this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTORDQTY) - parseInt(this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY);
            this.incode.setVal("");
            this.fnInit();
          } else {
            Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
            this.incodeMessage = this.message.message["ME000025"].replace("%1", this.formItemNm[8]);
            this.incode.setVal("");
            this.incode.focusIn();
            this.incode.selectAll();
            return;
          }
        } else {
          let existFlg: boolean = false;
          if (this.lstShipment[this.FTOT0020Store.NowIndex].ITEMCD == response.stot0020ConditionDto.ITEMCD) {
            existFlg = true;
          } else {
            existFlg = false;
            this.FTOT0020Store.LotChckFlg = 0;
            for (let i = 0; i < this.lstShipment.length; i++) {
              if (this.lstShipment[i].ITEMCD == response.stot0020ConditionDto.ITEMCD) {
                this.FTOT0020Store.NowIndex = i;
                existFlg = true;
                break;
              }
            }
          }
          if (existFlg == false) {
            Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
            this.incodeMessage = this.message.message["ME000024"].replace("%1", this.formItemNm[8]);
            this.incode.setVal("");
            this.incode.focusIn();
            this.incode.selectAll();
            return;
          }
          if (this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTAVAILABLEQTY <= 0) {
            Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
            this.incodeMessage = this.message.message["ME000025"].replace("%1", this.formItemNm[8]);
            this.incode.setVal("");
            this.incode.focusIn();
            this.incode.selectAll();
            return;
          } else {
            this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTYBEFORE = this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY;
            this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY = parseInt(this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY) + 1;
            this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTAVAILABLEQTY = parseInt(this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTORDQTY) - parseInt(this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY);
          }
          this.FTOT0020Store.lstShipment = this.lstShipment;
          if (this.lstShipment[this.FTOT0020Store.NowIndex].LOTFLG == "0") {
            this.incode.setVal("");
            this.incode.focusIn();
            this.fnInit();
          } else if (this.lstShipment[this.FTOT0020Store.NowIndex].LOTFLG == "1" && this.FTOT0020Store.LotChckFlg == 0) {
            this.router.navigate([DisplayScreenID.STOT00205]);
          } else {
            this.incode.setVal("");
            this.incode.focusIn();
            this.fnInit();
          }
        }
      }
    })
  }

  //func click enter 
  fnEntInput($event: any, currentFocus: string) {
    switch (currentFocus) {
      case 'incode':
        this.fnScan();
        break;
    }
  }
}
