import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0020StoreService } from 'src/app/services/FTOT0020/ftot0020-store.service';

@Component({
  selector: 'app-stot00208',
  templateUrl: './stot00208.component.html'
})
export class STOT00208Component extends BaseComponent implements OnInit {


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0020Store: FTOT0020StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00208;
  CSTMRSVNO: string = "";
  DeliBoxQty: number = 0;
  lstDeliBoxSize: any = [];
  sizeBoxList: any = [];
  DeliBoxPage: number = 1;

  fnInit() {
    this.CSTMRSVNO = this.FTOT0020Store.lstShipment[0].CSTMRSVNO;
    this.DeliBoxPage = 1;
    this.DeliBoxQty = this.FTOT0020Store.DELIBOXQTY;
    this.fnGetData();
  }

  fnDestroy() {
  }

  fnGetData() {
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(this.DisplayScreenID),
      "stot0020ConditionDto": {
        "DELIMTHDCD": this.FTOT0020Store.DELIMTHDCD,
      }
    }
    return this.webService.callWs(Const.stot00208GetDataWs, request, (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning)
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
        })
        return;
      } else {
        this.lstDeliBoxSize = response.lstDeliBox;
      }
    })
  }


  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00207]);
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }


  btnClickBoxSize(itemcd: any) {
    var check = this.DeliBoxPage;
    var checkComFlg:boolean = false;
    if (check < this.DeliBoxQty) {
      this.sizeBoxList.push(itemcd)
      this.DeliBoxPage = this.DeliBoxPage + 1;
    } else if (check == this.DeliBoxQty) {
      this.sizeBoxList.push(itemcd)
      checkComFlg = true;
    }
    this.FTOT0020Store.SIZEBOXLIST = this.sizeBoxList;
    if (checkComFlg == true) {
      var MaxCount = this.FTOT0020Store.DELIBOXQTY;
      for (let i = 0; i < MaxCount; i++) {
        if (i == 0) {
          this.FTOT0020Store.DELIBOXITEMCD = this.FTOT0020Store.SIZEBOXLIST[i];
        } else {
          this.FTOT0020Store.DELIBOXITEMCD = this.FTOT0020Store.DELIBOXITEMCD + "," + this.FTOT0020Store.SIZEBOXLIST[i];
        }
      }
      if (this.FTOT0020Store.ScmFlg == "0" && (this.FTOT0020Store.lstShipment[0].DELIMTHDCD == "1101" || this.FTOT0020Store.lstShipment[0].DELIMTHDCD == "1103")) {
        this.UpdDenpyou(1);
      } else if (this.FTOT0020Store.ScmFlg == "1" && (this.FTOT0020Store.lstShipment[0].DELIMTHDCD == "1101" || this.FTOT0020Store.lstShipment[0].DELIMTHDCD == "1103")) {
        this.UpdDenpyou(2);
      } else {
        this.UpdDenpyou(3);
      }
    }
  }

  UpdDenpyou(modeFlg: any) {
    var INQNO = this.FTOT0020Store.lstShipment[0].INQNO;
    var request = {
      "accessInfo": this.loginInfo.getAccessInfo(DisplayScreenID.STOT00208),
      "stot0020ConditionDto": {
        "SOORDNO": this.FTOT0020Store.SOORDNO,
        "HHTIP": this.FTOT0020Store.HHTIP,
        "COMPUTERCD": this.FTOT0020Store.COMPUTERCD,
        "PRINTERCD": this.FTOT0020Store.PRINTERCD,
        "DELIMTHDSCMCD": this.FTOT0020Store.lstShipment[0].DELIMTHDCD,
        "DELIMTHDCD": this.FTOT0020Store.DELIMTHDCD,
        "PAGENUM": this.FTOT0020Store.DELIBOXQTY,
        "SYSKBN1": this.getSystemEntity("SYSKBN1"),
        "INQNO": Ultility.isEmpty(INQNO) ? "" : INQNO,
        "MODEFLG": modeFlg,
        "DELIBOXQTY": this.FTOT0020Store.DELIBOXQTY,
        "DELIBOXITEMCD": this.FTOT0020Store.DELIBOXITEMCD,
        "PCKNGLSTNO": this.FTOT0020Store.lstShipment[0].PCKNGLSTNO,
      }
    }
    return this.webService.callWs(Const.stot00208UpdDenpyouWs, request, async (response) => {
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        Ultility.handyCtrl(this.ledService, Const.LedRed, true, Const.SoundWarning);
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {
          return;
        });
      } else {
        this.notifySuccess(this.appService, this.messageEntity.message["MI000008"], () => {
          this.FTOT0020Store.BACKSCREEN = "STOT00208";
          this.router.navigate([DisplayScreenID.STOT00209])
        })
      }
    });
  }

}
