import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { Ultility } from 'src/app/services/common/ultility.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ftot0090StoreService} from 'src/app/services/FTOT0090/ftot0090-store.service'

@Component({
  selector: 'app-stot00901',
  templateUrl: './stot00901.component.html',
  styleUrls: ['./stot00901.component.css']
})
export class Stot00901Component  extends BaseComponent implements OnInit {

  flgScan = "";
  lst = [];
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00901;
  dtoStot00901!: Ftot0090StoreService | null;
  @ViewChild("incode") incode: InputComponent | any;

  strStatus = "";
  showbtnUpdate = false;
  disabelbtnNext = false;
  showlistlctn = true;
  showinputkey = true;
  itemcdsession = ""

  lctncdMode : any;
  qltycdMode : any;
  limitdateMode: any;
  stckmngkey1Mode : any;
  stckmngkey2Mode : any;
  stckmngkey3Mode : any;
  itemgrpcdMode : any;
  itemcdMode : any;
  itemnmMode : any;
  stckstscdMode : any;
  prestckstscdMode : any;
  allwqtyMode : any;
  stckqtyMode : any;
  divkbnMode : any;
  sirsltdateMode : any;
  firstsirsltdateMode : any;
  allwlimitMode : any;
  entdatetimeMode : any;
  upddatetimeMode : any;

  qltycdlst = [
    {
        "QLTYCD": "01",
        "QLTYNM": "",
        "QLTYRNM": ""
    },
    {
        "QLTYCD": "09",
        "QLTYNM": "",
        "QLTYRNM": ""
    }
  ];
  
  incodeMessage = "";
  fnInit(isMenu: boolean) {
    // if(this.stot00901Service.flg === false) {
    //   this.flgScan = "0";
    //   this.incode.setVal(this.stot00901Service.ITEMCD);
    //   this.checkItemcd(this.stot00901Service.ITEMCD);
    //   this.stot00901Service.flg = true;
    // }
    if(this.stot00901Service.STATUS === 1) {
      this.strStatus = "Quet  ITEMCD";
    }

  }
  fnDestroy() {
    
  }

  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    private stot00901Service: Ftot0090StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }
  // Control input enter key
  fnEntInput($event: any, currentFocus: string) {
    if(currentFocus === 'incode') {
      switch(this.stot00901Service.STATUS) {
        case 1: {
          this.flgScan = "0";
          this.checkItemcd(this.incode.getVal());
          break;
        }
        case 2 : {
          this.flgScan = "0";
          this.checkLctn(this.incode.getVal());
          break;
        }
      }

    }
  }

  fncheckStckqty($event: any) {
    
    if ($event.target.value == "" && this.dtoStot00901) {
      console.log("rong");
      this.dtoStot00901.STCKQTY = "0";
    }
  }

  btnUpdatelctn() {
    var request = {
      "accessInfo" : this.loginInfo.getAccessInfo(DisplayScreenID.STOT00901) ,
      "stot00901DetailDto" : {
        "ITEMCD" : this.dtoStot00901?.ITEMCD,
        "LCTNCD" : this.dtoStot00901?.LCTNCD,
        "QLTYCD" : this.dtoStot00901?.QLTYCD,
        "STCKQTY" : this.dtoStot00901?.STCKQTY,
        "STCKMNGKEY1": this.dtoStot00901?.STCKMNGKEY1,
        "STCKMNGKEY2": this.dtoStot00901?.STCKMNGKEY2,
        "STCKMNGKEY3": this.dtoStot00901?.STCKMNGKEY3 
      }
    }

    return this.webService.callWs(Const.stot00901UpdateLctnWs, request, (response)=>{
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {})
        return;
      } else {
         this.notifySuccess(this.appService, "Update sucess !", () => {})
         this.dtoStot00901 = response.stot00901DetailDto;
         this.incode.setVal("");
      }
    })

  }

  checkLctn(lctn: any) {
    //this.lst = [];
    var request = {
      "accessInfo" : this.loginInfo.getAccessInfo(DisplayScreenID.STOT00901) ,
      "stot0090Condition" : {
        "ITEMCD": this.stot00901Service.ITEMCD,
        "LCTNCD" : lctn,
        "FLGSCAN" : this.flgScan
      }
    }

    return this.webService.callWs(Const.stot00901CheckLctnWs, request, (response)=>{
      if (Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)) {
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {})
        this.dtoStot00901 = null;
        this.showbtnUpdate = false;
        this.incode.setVal("");
        return;
      } else {

        console.log(response);
        this.dtoStot00901 = response.stot00901DetailDto;
        console.log(this.dtoStot00901!.ITEMCD);
        this.showbtnUpdate = true;
        this.showinputkey = false;
        this.stot00901Service.STATUS = 3;
        this.showlistlctn = false;
        this.disabelbtnNext = true;
        this.strStatus = "Update Detail";
      }
    })

  }
  

  checkItemcd(itemcd: any) {
    var request = {
      "accessInfo" : this.loginInfo.getAccessInfo(DisplayScreenID.STOT00901) ,
      "stot0090Condition" : {
        "ITEMCD" : itemcd,
        "FLGSCAN" : this.flgScan
      }
    }

    return this.webService.callWs(Const.stot00901CheckItemcdWs, request, (response)=>{
      if(Ultility.fnSetErrorMsg(this, this.ledService, response.fatalError)){
        this.notifyError(this.appService, response.fatalError[0].errMsg, () => {})
        return;
      }else{
         this.lst = response.listLctn;
         this.stot00901Service.ITEMCD = itemcd;
         this.itemcdsession = itemcd;
         this.stot00901Service.STATUS = 2;
        
         console.log(this.stot00901Service.STATUS);
         this.incode.setVal("");
         this.strStatus = "Quet LCTN";
      }
    })
  }

  btnBack() {
    if (this.stot00901Service.STATUS == 2) {
      this.stot00901Service.STATUS = 1;
      this.stot00901Service.flg = false;
      this.strStatus = "Quet  ITEMCD";
      this.itemcdsession = "";
      this.incode.setVal("");
      this.dtoStot00901 = null;
      this.showbtnUpdate = false;
      this.lst = [];
    } else if(this.stot00901Service.STATUS == 3){
      this.stot00901Service.STATUS = 2;
      this.showlistlctn = true;
      this.showbtnUpdate = true;
      this.showinputkey = true;
      this.disabelbtnNext = false;
      this.showbtnUpdate = false;
      this.dtoStot00901 = null;
      this.strStatus = "Quet LCTN";
      this.incode.setVal("");
    } else {
      this.lst = [];
      this.backToMenu();
    }

  }

  btnNext(){
    switch(this.stot00901Service.STATUS) {
      case 1 : {
        this.flgScan = "0";
        this.checkItemcd(this.incode.getVal());
        break;
      }
      case 2: {
        this.flgScan = "0";
        this.checkLctn(this.incode.getVal());
        break;
      }
    }
  }

  fnClickDeltai(lst:any) {
    console.log(lst);
    this.stot00901Service.flg = false;
    this.stot00901Service.ALLWLIMIT = lst["ALLWLIMIT"];
    this.stot00901Service.ALLWQTY  = lst["ALLWQTY"]
    this.stot00901Service.DIVKBN = lst["DIVKBN"]
    this.stot00901Service.ENTDATETIME = lst["ENTDATETIME"]
    this.stot00901Service.FIRSTSIRSLTDATE = lst["FIRSTSIRSLTDATE"]
    this.stot00901Service.INSTRCD = lst["INSTRCD"]
    this.stot00901Service.ITEMCD = lst["ITEMCD"]
    this.stot00901Service.ITEMGRPCD = lst["ITEMGRPCD"]
    this.stot00901Service.ITEMNM = lst["ITEMNM"]
    this.stot00901Service.LCTNCD = lst["LCTNCD"]
    this.stot00901Service.LIMITDATE = lst["LIMITDATE"]
    this.stot00901Service.PRESTCKSTSCD = lst["PRESTCKSTSCD"]
    this.stot00901Service.QLTYCD = lst["QLTYCD"]
    this.stot00901Service.SIRSLTDATE = lst["SIRSLTDATE"]
    this.stot00901Service.STCKMNGKEY1 = lst["STCKMNGKEY1"]
    this.stot00901Service.STCKMNGKEY2 = lst["STCKMNGKEY2"]
    this.stot00901Service.STCKMNGKEY3 = lst["STCKMNGKEY3"]
    this.stot00901Service.STCKQTY = lst["STCKQTY"]
    this.stot00901Service.STCKSTSCD = lst["STCKSTSCD"]
    this.stot00901Service.UPDDATETIME = lst["UPDDATETIME"]
    this.router.navigate(['STOT00902']);
  }

}
