import { Component, Injector, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';
import { AppService } from 'src/app/services/common/app.service';
import { LedService } from 'src/app/services/common/led.service';
import { LoginInfoService } from 'src/app/services/common/login-info.service';
import { MenuService } from 'src/app/services/common/menu.service';
import { MessageService } from 'src/app/services/common/message.service';
import { SystemEntityService } from 'src/app/services/common/system-entity.service';
import { WebService } from 'src/app/services/common/webservice.service';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../../base/input/input-text.component';
import * as Const from '../../../common/Const';
import { Ultility } from 'src/app/services/common/ultility.service';
import { IObjectString } from 'src/app/common/IObject';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FTOT0020StoreService } from 'src/app/services/FTOT0020/ftot0020-store.service';

@Component({
  selector: 'app-stot00210',
  templateUrl: './stot00210.component.html'
})
export class STOT00210Component extends BaseComponent implements OnInit {


  constructor(protected loginInfo: LoginInfoService,
    protected webService: WebService,
    protected menu: MenuService,
    private modalService: NgbModal,
    protected router: Router,
    protected message: MessageService,
    protected systemEntity: SystemEntityService,
    private appService: AppService,
    protected ledService: LedService,
    protected FTOT0020Store: FTOT0020StoreService,
    protected injector: Injector) {
    super(loginInfo, webService, menu, ledService, router, systemEntity, message, injector);
  }
  incodeMessage: string = "";
  DisplayScreenID: DisplayScreenID = DisplayScreenID.STOT00210;
  ITEMNM: string = "";
  SHIPMNTORDQTY: string = "";
  SHIPMNTRSLTQTY: string = "";
  GIFTDETAIL: string = "";
  LIQUIDDETAIL: string = "";
  BROKENDETAIL: string = "";
  WRAPPINGFLG: string = "";
  HEMMING: string = "";
  ADDITEMFLG: string = "";
  SIZE: string = "";
  COLOR: string = "";
  lstShipment: any = [];
  optionlsttemp: any = [];
  optionlst: string = "";
  linkImg: string = "";

  fnInit() {
    this.lstShipment = this.FTOT0020Store.lstShipment;
    this.ITEMNM = this.lstShipment[this.FTOT0020Store.NowIndex].ITEMNM;
    this.SHIPMNTORDQTY = this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTORDQTY;
    this.SHIPMNTRSLTQTY = this.lstShipment[this.FTOT0020Store.NowIndex].SHIPMNTRSLTQTY;
    this.GIFTDETAIL = this.lstShipment[this.FTOT0020Store.NowIndex].GIFTDETAIL == 1 ? this.formItemNm[8] : '';
    this.LIQUIDDETAIL = this.lstShipment[this.FTOT0020Store.NowIndex].LIQUIDDETAIL == 1 ? this.formItemNm[9] : '';
    this.BROKENDETAIL = this.lstShipment[this.FTOT0020Store.NowIndex].BROKENDETAIL == 1 ? this.formItemNm[10] : '';
    this.WRAPPINGFLG = this.lstShipment[this.FTOT0020Store.NowIndex].WRAPPINGFLG == 1 ? this.formItemNm[11] : '';
    this.HEMMING = this.lstShipment[this.FTOT0020Store.NowIndex].HEMMING == 1 ? this.formItemNm[12] : '';
    this.ADDITEMFLG = this.lstShipment[this.FTOT0020Store.NowIndex].ADDITEMFLG == 1 ? this.formItemNm[13] : '';
    this.optionlst = this.lstShipment[this.FTOT0020Store.NowIndex].OPTION;
    this.optionlsttemp = this.FTOT0020Store.OPTIONLST;
    this.linkImg = "http://webcoban.vn/image/flower.gif";

    if (!Ultility.isEmpty(this.optionlst)) {
      this.optionlsttemp = this.FTOT0020Store.OPTIONLST;
    } else {
      this.optionlsttemp = [];
    }
  }

  fnDestroy() {
  }

  btnBack() {
    this.router.navigate([DisplayScreenID.STOT00209]);
  }

  fnFocusInput($event: any, currentFocus: any) {
    this.appService.storedInput = currentFocus;
  }

  //func click enter 
  fnEntInput($event: any, currentFocus: string) {
  }
}
