import { Injectable, NgZone } from '@angular/core';

import { isDevMode } from '@angular/core';
import { AuthKbnService } from './authkbn.service';
import { MessageService } from './message.service';
import { MenuService } from './menu.service';
import { DisplayScreenID } from 'src/app/common/DisplayScreenId';

/** */
@Injectable({
  providedIn: 'root'
})
export class LoginInfoService {
  static readonly LOCAL_STORAGE_NAME = 'LoginInfoData';
  /** */
  usercd = '';
  /** */
  usernm = '';
  /** */
  lang: string = '';
  /** */
  authgrp = '';
  /** */
  token = '';
  /** */
  key = '';
  cstmcd = '';
  brnchcd = '';
  cstmnm = '';
  brnchnm = '';


  /**
   * 
   * clearclear
   */
  constructor(private authKbn: AuthKbnService
    , private message: MessageService
    , private menu: MenuService
  ) {

  }

  /**
   * clear
   */
  public clear() {
    this.usercd = '';
    this.usernm = '';
    this.lang = '';
    this.authgrp = '';
    this.token = '';
    this.key = '';
    this.cstmcd = '';
    this.brnchcd = '';
    this.cstmnm = '';
    this.brnchnm = '';

    this.authKbn.clear();
    this.message.clear();
    this.menu.clear();

    localStorage.removeItem(LoginInfoService.LOCAL_STORAGE_NAME);
  }
  /**
   * API
   */
  public getAccessInfo(scrnid: DisplayScreenID): any {
    return {
        CSTMCD: this.cstmcd,
        BRNCHCD: this.brnchcd,
        USRCD: this.usercd,
        USRNM: this.usernm,
        LANG: this.lang,
        TOKEN: this.token,
        KEY: this.key,
    };
  }

  /**
   * 
   */
  public isLogin(): boolean {
    return this.token != undefined && this.token.length > 0;
  }
}
