import { Component, Input, OnInit, ElementRef, Output, EventEmitter } from '@angular/core';
import * as Const from 'src/app/common/Const';

@Component({
  selector: 'app-input-number',
  templateUrl: './input-number.component.html'
})
export class InputNumberComponent implements OnInit {

  constructor(private el: ElementRef) { 
    
  }
  inputmode: string = Const.InputMode.NONE;
  // Input model: ngModel angular
  inputModel: number | undefined = 0;
  storeInputVal: number | undefined = 0;

  inputId: any;

  // Emit function
  @Output() inputKeyEnt = new EventEmitter();
  @Output() inputBlurEvent = new EventEmitter();
  @Output() inputFocusEvent = new EventEmitter();

  ngOnInit(): void {
    this.inputId = this.el.nativeElement.id;
  }

  /******************************************
   * SHOW HIDE KEYBOARD ON FOCUS INPUT
   ******************************************/
  keyboardClick = () => {
    if (this.inputmode == Const.InputMode.NONE) {
      this.inputmode = 'numeric';

      var elm = this.el.nativeElement as HTMLInputElement;
      var input = elm.children[0].firstChild as HTMLInputElement;
      input.focus();
      input.select();
    }
  }

  /******************************************
   * SET INPUTMODE TO 'NONE' WHEN FOCUS OUT
   ******************************************/
  inputBlur = () => {
    this.inputmode = Const.InputMode.NONE;
    this.storeInputVal = this.inputModel;
    this.inputBlurEvent.emit();
  }

  inputFocus = () => {
    this.storeInputVal = this.inputModel;
    this.inputFocusEvent.emit();

    this.focusIn();
  }

  /******************************************
   * SET VALUE FROM PARENT TO INPUT
   ******************************************/
  setVal(val: any) {
    this.inputModel = val;
    this.storeInputVal = val;
  }

  /******************************************
   * GET INPUT CURRENT VALUE
   ******************************************/
  getVal() {
    return this.inputModel;
  }

  getStoredVal(){
    return this.storeInputVal;
  }

  /******************************************
   * SET FOCUSIN INPUT
   ******************************************/
  focusIn() {
    var elm = this.el.nativeElement as HTMLInputElement;
    var input = elm.children[0].firstChild as HTMLInputElement;
    input.focus();
    setTimeout(function(){
      input.select();
    })
  }
  selectAll() {
    var elm = this.el.nativeElement as HTMLInputElement;
    var input = elm.children[0].firstChild as HTMLInputElement;
    setTimeout(function(){
      input.select();
    })
  }

  /******************************************
   * EMIT WHEN ENT PRESS
   ******************************************/
  inputKeyEntFunc = ($event: any) => {
    this.inputKeyEnt.emit($event);
  }

  keyPress(event: any) {
    // if (!this.integerCheck(event.key)) {
    //   event.returnValue = false;
    //   if (event.preventDefault) event.preventDefault();
    // }
  }
  onPaste(event: ClipboardEvent) {
    // let clipboardData = event.clipboardData;
    // let pastedText = clipboardData!.getData('text');
    // console.log(pastedText);

    // if (!this.integerCheck(pastedText)) {
    //   event.returnValue = false;
    //   if (event.preventDefault) event.preventDefault();
    // }
  }
  integerCheck = (val: any) => {
    // var regex: RegExp  = /^\d+$/;
    // return regex.test(val);
  }
}
